<template>
    <div>
        <el-dialog
            title=""
            width="40%"
            :visible="dialogVisible"
            :append-to-body="true"
            :show-close="false"
        >
            <div class="my-dialog-body">
                <div> </div>

                <div v-if="suiteTypeForDel == 'sprint'">
                    <br />
                    <div>
                        <el-radio v-model="delBaseSuitesCascade" label="1"></el-radio>
                        <el-radio v-model="delBaseSuitesCascade" label="2"></el-radio>
                    </div>
                    <br />
                    <div></div>
                </div>
            </div>

            <div class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="confirm"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props: ["dialogVisible", "suiteIdForDel", "suiteTypeForDel"],
    data() {
        return {
            delBaseSuitesCascade: "1", // 
        };
    },
    methods: {
        closeDialog() {
            this.$emit("update:dialogVisible", false); // 
        },
        cancel() {
            this.closeDialog();
        },
        confirm() {
            this.$api.sprintCaseSuite
                .deleteSprintCaseSuite({
                    suiteId: this.suiteIdForDel,
                    type: this.suiteTypeForDel,
                    delBaseSuitesCascade: this.delBaseSuitesCascade,
                })
                .then((res) => {
                    if (res.success) {
                        this.$parent.removeCurrNodeClicked();
                        this.$message.success(res.msg);
                    } else {
                        this.$message.error(res.msg);
                    }

                    this.closeDialog();
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
    },
};
</script>

<style>

</style>



