<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryForm" size="small">
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.name"
                        placeholder=""
                        clearable
                        style="width:240px"
                    ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.version"
                        placeholder=""
                        clearable
                        style="width:230px"
                    ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="queryForm.status" clearable style="width: 100px;">
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :value="item.value"
                            :label="item.label"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="querySprints('clickQuery')"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button :size="buttonSize" @click="addSprint"></el-button>
                <el-button :size="buttonSize" @click="deleteRows($api.sprint.deleteSprints)"></el-button>
            </el-button-group>
        </div>
        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            border
            stripe
            :empty-text="emptyText"
            class="my-table"
            :cell-class-name="getCellClassName"
            @selection-change="onSelectChange"
            highlight-current-row
        >
            <!---->
            <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
            <!-- <el-table-column prop="id" label="ID" width="70px" header-align="center" align="center"></el-table-column> -->
            <el-table-column
                prop="name"
                label=""
                header-align="center"
                align="left"
                show-overflow-tooltip
                min-width="180px"
            ></el-table-column>
            <el-table-column
                prop="version"
                label=""
                header-align="center"
                show-overflow-tooltip
                align="left"
                width="130px"
            ></el-table-column>
            <el-table-column
                prop="status"
                label=""
                width="80px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="beginTime"
                label=""
                width="110px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="endTime"
                label=""
                width="110px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="desc"
                label=""
                header-align="center"
                align="left"
                min-width="250px"
                show-overflow-tooltip
            ></el-table-column>
            <el-table-column
                prop="createrName"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updaterName"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updateTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column label="" align="left" width="215px">
                <template slot-scope="scope">
                    <el-button size="mini" @click="editSprint(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" @click="copySprint(scope.$index, scope.row)"></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteRow(scope.$index, scope.row, $api.sprint.deleteSprint)"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!-- \ -->
        <sprint-dialog
            v-if="sprintDialogVisible"
            :dialogVisible.sync="sprintDialogVisible"
            :dialogTitle="sprintDialogTitle"
            :tableData="tableData"
            :statusOptions="statusOptions"
            :row="row"
            :dialogFormData="dialogFormData"
        ></sprint-dialog>
    </div>
</template>

<script>
import SprintDialog from "./SprintDialog";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

export default {
    mixins: [elTableMixin, elButtonMixin],
    props: ["productID"],
    data() {
        return {
            // 
            queryForm: {
                productId: this.productID,
                name: "", // 
                version: "", // 
                status: "", // 
            },
            dialogFormData: null, // 
            statusOptions: [
                // 
                { label: "", value: "" },
                { label: "", value: "" },
                { label: "", value: "" },
            ],

            sprintDialogVisible: false, // \true -  false - 
            sprintDialogTitle: "", // 
        };
    },
    components: {
        SprintDialog,
    },
    methods: {
        querySprints(action) {
            if (!this.productID) {
                this.total = 0;
                this.tableData = [];
                return;
            }
            this.queryRows(action);
        },
        addSprint() {
            if (!this.productID) {
                this.$message.error("");
                return;
            }

            if (!this.sprintDialogVisible) {
                this.dialogFormData = {
                    name: "", //
                    version: "", // 
                    productId: this.productID,
                    beginTime: "", // 
                    endTime: "", // 
                    desc: "", // 
                    status: "", // 
                };

                this.sprintDialogVisible = true;
                this.sprintDialogTitle = "";
            }
        },
        copySprint(index, row) {
            if (!this.sprintDialogVisible) {
                this.dialogFormData = {
                    name: row.name,
                    version: row.version,
                    productId: row.productId,
                    beginTime: row.beginTime,
                    endTime: row.endTime,
                    desc: row.desc,
                    status: row.status,
                };
                this.sprintDialogVisible = true;
                this.sprintDialogTitle = "";
            }
        },
        editSprint(index, row) {
            if (!this.sprintDialogVisible) {
                this.dialogFormData = {
                    id: row.id,
                    productId: row.productId,
                    name: row.name,
                    version: row.version,
                    productId: row.productId,
                    beginTime: row.beginTime,
                    endTime: row.endTime,
                    desc: row.desc,
                    status: row.status,
                };
                this.row = row;
                this.sprintDialogVisible = true;
                this.sprintDialogTitle = "";
            }
        },
    },
    updated() {
        this.setTableBodySize();
    },
    created() {
        this.queryRowsAPI = this.$api.sprint.getSprints;
        this.querySprints();
    },
};
</script>

<style lang="scss" scoped>
</style>



