<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button :size="buttonSize" @click="addEnv"></el-button>
                <el-button :size="buttonSize" @click="deleteRows($api.env.deleteEnvs)"></el-button>
            </el-button-group>
        </div>

        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            :empty-text="emptyText"
            border
            stripe
            class="my-table"
            :cell-class-name="getCellClassName"
            @selection-change="onSelectChange"
            highlight-current-row
        >
            <!---->
            <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
            <!-- <el-table-column prop="id" label="ID" width="70px" header-align="center" align="center"></el-table-column> -->
            <el-table-column
                prop="name"
                label=""
                header-align="center"
                align="left"
                min-width="250px"
            ></el-table-column>
            <el-table-column
                prop="desc"
                label=""
                header-align="center"
                align="left"
                min-width="400px"
            ></el-table-column>
            <el-table-column
                prop="createrName"
                label=""
                min-width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                min-width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updaterName"
                label=""
                min-width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updateTime"
                label=""
                min-width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column label="" align="left" width="160px">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="editEnv(scope.$index, scope.row)"
                    ></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteRow(scope.$index, scope.row, $api.env.deleteEnv)"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!-- \ -->
        <env-dialog
            v-if="dialogVisible"
            :dialogVisible.sync="dialogVisible"
            :dialogTitle="dialogTitle"
            :tableData="tableData"
            :dialogFormData="envData"
            :row="row"
        ></env-dialog>
    </div>
</template>

<script>
import EnvDialog from "./Env/EnvDialog";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

export default {
    mixins: [elTableMixin, elButtonMixin],
    name: "Env",
    data() {
        return {
            envData: null, // 
            dialogVisible: false, // \true -  false - 
            dialogTitle: "", // 
        };
    },
    components: {
        EnvDialog,
    },
    methods: {
        addEnv() {
            if (!this.dialogVisible) {
                this.envData = {
                    name: "",
                    desc: "",
                };

                this.dialogVisible = true;
                this.dialogTitle = "";
            }
        },
        editEnv(index, row) {
            if (!this.dialogVisible) {
                this.envData = {
                    id: row.id,
                    name: row.name,
                    desc: row.desc,
                };
                this.row = row;
                this.dialogVisible = true;
                this.dialogTitle = "";
            }
        },
    },
    updated() {
        this.setTableBodySize();
    },

    created() {
        this.queryRowsAPI = this.$api.env.getEnvs;
        this.queryRows();
    },
};
</script>

<style lang="scss">
</style>



