<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryForm" size="small">
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.name"
                        placeholder=""
                        clearable
                        style="width:230px"
                    ></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-select
                        v-model="queryForm.isActive"
                        clearable
                        @clear="clearStatus"
                        style="width: 100px;"
                    >
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :value="item.value"
                            :label="item.label"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="queryRows('clickQuery')"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button :size="buttonSize" @click="addRole"></el-button>
                <el-button
                    :size="buttonSize"
                    @click="deleteRows($api.sysRole.batchDeleteSysRole)"
                ></el-button>
            </el-button-group>
        </div>
        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            border
            stripe
            :empty-text="emptyText"
            class="my-table"
            :cell-class-name="getCellClassName"
            @selection-change="onSelectChange"
            @row-click="clickRow"
            highlight-current-row
        >
            <!---->
            <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
            <!-- <el-table-column prop="id" label="ID" width="60px" header-align="center" align="center"></el-table-column> -->
            <el-table-column
                prop="name"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
            ></el-table-column>
            <el-table-column
                prop="desc"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
            ></el-table-column>
            <el-table-column
                prop="isActive"
                label=""
                width="80px"
                header-align="center"
                align="center"
            >
                <template slot-scope="scope">
                    <span v-if="scope.row.isActive"></span>
                    <span v-else></span>
                </template>
            </el-table-column>
            <el-table-column
                prop="createrName"
                label=""
                width="100px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updaterName"
                label=""
                width="100px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updateTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column label="" align="left" width="290px">
                <template slot-scope="scope">
                    <el-button size="mini" @click="bindMenu(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" @click="editRole(scope.$index, scope.row)"></el-button>
                    <el-button
                        v-if="scope.row.isActive"
                        size="mini"
                        @click="toggleRoleStatus(scope.$index, scope.row)"
                    ></el-button>
                    <el-button
                        v-else
                        size="mini"
                        @click="toggleRoleStatus(scope.$index, scope.row)"
                    ></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteRow(scope.$index, scope.row, $api.sysRole.deleteSysRole)"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!--  -->
        <sys-role-dialog
            v-if="roleDialogVisible"
            :dialogVisible.sync="roleDialogVisible"
            :dialogTitle="roleDialogTitle"
            :tableData="tableData"
            :dialogFormData="roleData"
            :row="row"
            :statusOptions="statusOptions"
        ></sys-role-dialog>

        <!--  -->
        <bind-menu-dialog
            v-if="bindMenuDialogVisible"
            :dialogVisible.sync="bindMenuDialogVisible"
            :roleID="roleID"
        ></bind-menu-dialog>
    </div>
</template>

<script>
import SysRoleDialog from "./SysRole/SysRoleDialog";
import BindMenuDialog from "./SysRole/BindMenuDialog";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

export default {
    mixins: [elTableMixin, elButtonMixin],
    data() {
        return {
            // 
            queryForm: {
                name: "", // 
                isActive: "", // 
            },
            roleData: null, // 
            roleDialogVisible: false, // \true -  false - 
            roleDialogTitle: "", // 
            bindMenuDialogVisible: false, // 
            roleID: 0, // id
            statusOptions: [
                { label: "", value: true },
                { label: "", value: false },
            ],
        };
    },
    components: {
        SysRoleDialog,
        BindMenuDialog,
    },
    methods: {
        // 
        addRole() {
            if (!this.roleDialogVisible) {
                this.roleData = {
                    name: "", // 
                    desc: "", // 
                    isActive: true, // 
                };

                this.roleDialogVisible = true;
                this.roleDialogTitle = "";
            }
        },
        // 
        editRole(index, row) {
            if (!this.roleDialogVisible) {
                this.roleData = {
                    id: row.id,
                    name: row.name, // 
                    desc: row.desc, // 
                    isActive: row.isActive, // 
                };
                this.roleDialogVisible = true;
                this.roleDialogTitle = "";
                this.row = row;
            }
        },
        // 
        toggleRoleStatus(index, row) {
            let targetStatus = row.isActive ? "" : "";
            this.$confirm("" + targetStatus + "?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.sysRole
                        .updateSysRole({
                            id: row.id,
                            isActive: !row.isActive,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                for (let key in res.data) {
                                    if (key in row) {
                                        // key
                                        row[key] = res.data[key];
                                    }
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        // 
        bindMenu(index, row) {
            if (!this.bindMenuDialogVisible) {
                this.bindMenuDialogVisible = true;
                this.roleID = row.id;
            }
        },
        // 
        clickRow(row, column, event) {
            if (column.label != "") {
                this.editRole(undefined, row);
            }
        },
        // isActive="",isActive
        clearStatus() {
            this.queryForm.isActive = "";
        },
    },
    updated() {
        this.setTableBodySize();
    },
    created() {
        this.queryRowsAPI = this.$api.sysRole.getSysRoles;
        this.queryRows();
    },
};
</script>

<style lang="scss">
</style>



