<template>
    <div
        class="view-wrapper"
        ref="viewWrapper"
        v-resize="setTableBodySize"
        v-loading="loading"
        :element-loading-text="elementLoadingText"
    >
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button
                    :size="buttonSize"
                    type="primary"
                    @click="updateCaseTestResults"
                    :disabled="showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="batchUpdateTestCases"
                    :disabled="showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="newCase"
                    :disabled="iscurrSuiteRoot || showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="deleteCaseInBatch"
                    :disabled="showCaseDialog"
                    ></el-button
                >

                <el-button
                    :size="buttonSize"
                    @click="exportTestCase"
                    :disabled="showCaseDialog"
                    ></el-button
                >

                <el-dropdown>
                    <el-button :size="buttonSize" :disabled="showCaseDialog">
                        
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-button-group>
            <el-button
                :size="buttonSize"
                type="primary"
                @click="assignCases"
                :disabled="showCaseDialog"
                ></el-button
            >
            <el-select
                v-model="batchAssignedTo"
                value-key="id"
                filterable
                clearable
                placeholder=""
                :size="buttonSize"
                style="width: 120px"
            >
                <el-option
                    v-for="item in userOptions"
                    :key="item.id"
                    :label="item.name"
                    :value="item"
                ></el-option>
            </el-select>
        </div>

        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryFrom" size="small">
                <!-- <el-form-item label="">
                    <el-date-picker
                        style="width:330px"
                        v-model="createTime"
                        type="datetimerange"
                        placeholder=""
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        size="mini"
                        :default-time="['00:00:00', '23:59:59']"
                        value-format="yyyy-MM-dd HH:mm:ss"
                    ></el-date-picker>
                </el-form-item>-->
                <el-form-item label="">
                    <el-select
                        v-model="queryFrom.result"
                        clearable
                        style="width: 95px"
                    >
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-select
                        v-model="queryFrom.executionPhase"
                        clearable
                        style="width: 110px"
                    >
                        <el-option
                            v-for="item in executionPhaseOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-select
                        v-model="queryFrom.assignedTo"
                        filterable
                        clearable
                        placeholder=""
                        size="small"
                        style="width: 120px"
                    >
                        <el-option
                            v-for="item in userOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <span v-if="!isQueryItemsCollapsed">
                    <el-form-item label="">
                        <el-input
                            v-model="queryFrom.caseName"
                            placeholder=""
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="queryFrom.priority"
                            :multiple="true"
                            clearable
                            style="width: 215px"
                        >
                            <el-option label="P1" value="P1"></el-option>
                            <el-option label="P2" value="P2"></el-option>
                            <el-option label="P3" value="P3"></el-option>
                            <el-option label="P4" value="P4"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="">
                        <el-select
                            v-model="queryFrom.executionMethod"
                            clearable
                            style="width: 95px"
                        >
                            <el-option
                                label=""
                                value="handwork"
                            ></el-option>
                            <el-option
                                label=""
                                value="automation"
                            ></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="">
                        <el-select
                            v-model="queryFrom.testerId"
                            filterable
                            clearable
                            placeholder=""
                            size="small"
                            style="width: 120px"
                        >
                            <el-option
                                v-for="item in userOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="queryFrom.recursive"
                            style="width: 60px"
                        >
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryFrom.tag"
                            placeholder=""
                            style="width: 150px"
                        ></el-input>
                    </el-form-item>
                </span>
                <el-form-item>
                    <el-button
                        type="primary"
                        @click="queryRows('clickQuery')"
                        :disabled="showCaseDialog"
                        ></el-button
                    >
                    <el-button @click="restQueryRowsForm"></el-button>
                </el-form-item>

                <span
                    @click.prevent="toggleQueryItems"
                    style="
                        height: 32px;
                        line-height: 32px;
                        font-size: 14px;
                        color: #606266;
                    "
                >
                    <i
                        class="el-icon-arrow-up el-icon--right"
                        v-show="!isQueryItemsCollapsed"
                        ></i
                    >
                    <i
                        class="el-icon-arrow-down el-icon--right"
                        v-show="isQueryItemsCollapsed"
                        ></i
                    >
                </span>
            </el-form>
        </div>

        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            :empty-text="emptyText"
            border
            stripe
            :lazy="false"
            row-key="guid"
            :span-method="mergeColumns"
            :cell-class-name="getCellClassName"
            :row-class-name="getRowClassName"
            class="my-table"
            @row-click="clickRow"
            @select="onSelectRow"
            @select-all="onSelectAllRow"
            @sort-change="sortChange"
            highlight-current-row
        >
            <!---->
            <el-table-column
                type="selection"
                :width="checkBoxColWidth"
                align="center"
            ></el-table-column>
            <!-- <el-table-column
                prop="customNo"
                label=""
                width="150px"
                header-align="center"
                align="left"
            ></el-table-column>-->
            <el-table-column
                prop="name"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
                sortable="custom"
                min-width="250px"
            ></el-table-column>
            <el-table-column
                prop="priority"
                label=""
                width="90px"
                header-align="center"
                align="center"
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="assignedTo"
                label=""
                width="115px"
                header-align="center"
                align="center"
            >
                <template slot-scope="scope">
                    <el-select
                        v-model="scope.row.assignedTo"
                        filterable
                        placeholder=""
                        size="mini"
                        value-key="id"
                        @change="onAssignedToChanged(scope.row)"
                        @visible-change="
                            onAssignedToVisibleChange($event, scope.row)
                        "
                    >
                        <el-option
                            v-for="item in userOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item"
                        ></el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column
                prop="result"
                label=""
                width="90px"
                header-align="center"
                align="center"
                class="test-result-class"
            >
                <template slot-scope="scope">
                    <el-select
                        v-model="scope.row.result"
                        placeholder=""
                        size="mini"
                        @change="onResultChanged(scope, scope.row)"
                        @visible-change="
                            onVisibleChange($event, scope.row.result)
                        "
                    >
                        <el-option
                            v-for="item in resultOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column
                prop="testerName"
                label=""
                width="80px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="remark"
                label=""
                width="250px"
                header-align="center"
                show-overflow-tooltip
                align="left"
            ></el-table-column>
            <el-table-column label="" align="left" width="175px">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="editRemark(scope.$index, scope.row)"
                        v-if="scope.row.remark"
                        ></el-button
                    >
                    <el-button
                        size="mini"
                        @click="editRemark(scope.$index, scope.row)"
                        v-else
                        ></el-button
                    >
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteOneCase(scope.$index, scope.row)"
                        ></el-button
                    >
                </template>
            </el-table-column>
        </el-table>

        <!--  -->

        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!---->
        <transition name="slide-fade">
            <sprint-case-dialog
                v-if="caseDialogLoaded"
                :caseDialogLoaded.sync="caseDialogLoaded"
                :caseDialogEditing.sync="caseDialogEditing"
                :caseDialogTitle.sync="caseDialogTitle"
                :caseDialogVisible.sync="showCaseDialog"
                :tableData.sync="tableData"
                :caseData="caseData"
                :row.sync="row"
                :currentPage.sync="currentPage"
                :pageSize="pageSize"
                :total.sync="total"
                :operation.sync="operation"
                :caseOrder.sync="caseOrder"
                :currentModule="currentModule"
                :resultOptions="resultOptions"
                :planRow="planRow"
                :priorityOptions="priorityOptions"
                :executionPhaseOptions="executionPhaseOptions"
                :executedEachSprintDict="executedEachSprintDict"
            ></sprint-case-dialog>
        </transition>

        <!--  -->
        <update-result-dialog
            v-if="updateResultDlgVisible"
            :dialogVisible.sync="updateResultDlgVisible"
            :resultOptions="resultOptions"
            :caseIDsSelected="caseIDsSelected"
            :planRow="planRow"
        ></update-result-dialog>

        <!---->
        <batch-update-cases-dlg
            v-if="updateCasesDlgVisible"
            :dialogVisible.sync="updateCasesDlgVisible"
            :casesInfoForUpdate="casesInfoForUpdate"
            :suiteId="suiteID"
            :executionPhaseOptions="executionPhaseOptions"
            :executedEachSprintDict="executedEachSprintDict"
        ></batch-update-cases-dlg>

        <!--  -->
        <remark-dialog
            v-if="remarkDialogVisible"
            :dialogVisible.sync="remarkDialogVisible"
            :row="row"
        ></remark-dialog>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";
import SprintCaseDialog from "../SprintCase/SprintCaseDialog";
import BatchUpdateCasesDlg from "../SprintCase/BatchUpdateCasesDlg";
import UpdateResultDialog from "./UpdateResultDialog";
import RemarkDialog from "./RemarkDialog";

export default {
    mixins: [elTableMixin, elButtonMixin],
    props: ["planRow"],
    components: {
        SprintCaseDialog,
        UpdateResultDialog,
        RemarkDialog,
        BatchUpdateCasesDlg,
    },
    data() {
        return {
            // 
            queryFrom: {
                // startTime: "", // 
                // endTime: "", // 
                caseName: "", // 
                priority: [], // 
                result: "", //
                executionPhase: "", // 
                executionMethod: "handwork", // 
                recursive: "1", // 
                testerId: "", // ID
                assignedTo: "", // ID
                // creater: "", // 
                // updater: "", // 
                tag: "", // 
                sort: "-id",
            },
            caseStepTableData: [], // 
            caseAttachmentList: [], // 
            caseData: null, // 
            pageSizes: [10, 15, 20, 25, 30, 40, 50, 100, 150], //
            pageSize: 15, // 
            showCaseDialog: false, // / true false 
            caseDialogLoaded: false, //  true  false 
            caseDialogEditing: false, //  true  false 
            caseDialogTitle: "", // 
            operation: "viewCase", //   viewCase  newCase 
            // suitePath: "", // 
            caseIDsSelected: [], // ID
            caseOrder: 0, // ,, 1
            caseTestResult: "", // 

            // 
            resultOptions: [
                {
                    value: "",
                    label: "",
                },
                {
                    value: "",
                    label: "",
                },
                {
                    value: "",
                    label: "",
                },
                {
                    value: "",
                    label: "",
                },
            ],
            updateResultDlgVisible: false, //  true -  false -
            remarkDialogVisible: false, //  true -  false -
            currentModule: "testPlanCaseTable", // 
            bindBugDialogVisible: false, //  true -  false -
            userOptions: [], // 
            assginedToOld: {}, // 
            batchAssignedTo: {}, // 
            priorityOptions: [
                { label: "P1", value: "P1" },
                { label: "P2", value: "P2" },
                { label: "P3", value: "P3" },
                { label: "P4", value: "P4" },
            ],
            executionPhaseOptions: [], // 
            executedEachSprintDict: {}, // 
            loading: false, // 
            elementLoadingText: "",
            updateCasesDlgVisible: false, // 
            casesInfoForUpdate: [], // 
            isQueryItemsCollapsed: true, // 
        };
    },
    methods: {
        // 
        restQueryRowsForm() {
            this.queryFrom.caseName = "";
            this.queryFrom.priority = [];
            this.queryFrom.executionPhase = "";
            this.queryFrom.executionMethod = "handwork";
            this.queryFrom.result = "";
            this.queryFrom.recursive = "1";
            this.queryFrom.testerId = "";
            this.queryFrom.assignedTo = "";
            this.queryFrom.tag = "";
        },
        // 
        queryRows(action, resolve, reject) {
            if (this.suiteID == -1 || this.suiteID == -2) {
                // suiteIDstate-1-2
                this.total = 0;
                this.tableData = [];
                return;
            }

            if (action == "clickQuery") {
                this.currentPage = 1;
            }

            let queryParams = Object.assign({}, this.queryFrom);
            queryParams["planId"] = this.planRow.id;
            queryParams["suiteId"] = this.suiteID;
            queryParams["suiteType"] = this.suiteType;
            queryParams["pageSize"] = this.pageSize;
            queryParams["pageNo"] = this.currentPage;
            queryParams.priority = queryParams.priority.join(",");

            // 
            this.$api.testPlanCaseTable
                .getSuiteCases(queryParams)
                .then((res) => {
                    if (res.success) {
                        this.total = res.data.total;
                        this.tableData = res.data.rows;
                        if (resolve) {
                            resolve(res.success);
                        }
                    } else {
                        this.$message.error(res.msg);
                        if (reject) {
                            reject(res.success);
                        }
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg);
                    if (reject) {
                        reject(res.success);
                    }
                });
        },
        // 
        mergeColumns({ row, column, rowIndex, columnIndex }) {
            if (row.children.length) {
                if (columnIndex == 1) {
                    return [1, 8];
                } else if (columnIndex > 1) {
                    return [0, 0];
                }
            }
        },
        // 
        getRowClassName(object) {
            if (object.row.children.length) {
                return "row-class-for-colspan";
            } else {
                return "row-class";
            }
        },
        // 
        expandAll() {
            this.$nextTick(function () {
                const els = this.$el.getElementsByClassName(
                    "el-table__expand-icon"
                );
                for (let i = 0; i < els.length; i++) {
                    if (
                        els[i]
                            .getAttribute("class")
                            .indexOf("el-table__expand-icon--expanded") == -1
                    ) {
                        // 
                        els[i].click();
                    }
                }
            });
        },

        // 
        // title 
        // editingStatus 
        openCaseDialog(title, editingStatus) {
            if (!this.caseDialogLoaded) {
                // 
                this.caseDialogLoaded = true;
            }

            this.caseDialogTitle = title;
            this.caseDialogEditing = editingStatus;

            if (!this.showCaseDialog) {
                // 
                this.showCaseDialog = true;
            }
        },
        // 
        updateCaseTestResults() {
            if (!this.updateResultDlgVisible) {
                let caseIDArray = []; // ID
                let rowsSelected = this.$refs.myTable.selection;
                for (let i = 0; i < rowsSelected.length; i++) {
                    if (!rowsSelected[i].children.length) {
                        // 
                        caseIDArray.push(rowsSelected[i].id);
                    }
                }

                if (!caseIDArray.length) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
                this.caseIDsSelected = caseIDArray;
                this.updateResultDlgVisible = true;
            }
        },
        // 
        onVisibleChange(event, result) {
            if (event) {
                // 
                this.caseTestResult = result;
            }
        },
        onResultChanged(scope, row) {
            this.$api.testPlanCaseTable
                .updateCaseTestResult({
                    id: row.id,
                    result: row.result,
                    planId: row.testplanId,
                })
                .then((res) => {
                    if (res.success) {
                        this.$message.success(res.msg);

                        // 
                        for (let key in res.data.caseDataUpdated) {
                            if (key in row) {
                                // key
                                row[key] = res.data.caseDataUpdated[key];
                            }
                        }
                        this.setTestResultStyle(scope.$index);

                        // 
                        for (let key in res.data.planDataUpdated) {
                            if (key in this.planRow) {
                                // key
                                this.planRow[key] =
                                    res.data.planDataUpdated[key];
                            }
                        }
                    } else {
                        // 
                        row["result"] = this.caseTestResult;
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    row["result"] = this.caseTestResult;
                    this.$message.error(res.msg);
                });
        },
        // 
        batchUpdateTestCases() {
            if (this.showCaseDialog) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }
            this.elementLoadingText = "";
            this.casesInfoForUpdate = []; // guid
            let rowsSelected = this.$refs.myTable.selection;
            for (let i = 0; i < rowsSelected.length; i++) {
                if (!rowsSelected[i].children.length) {
                    // 
                    this.casesInfoForUpdate.push({
                        guid: rowsSelected[i].guid,
                        suiteId: rowsSelected[i].suiteId,
                        suite_type: rowsSelected[i].suiteType,
                        detailSuiteType: rowsSelected[i].detailSuiteType,
                        testplanId: rowsSelected[i].testplanId,
                    });
                }
            }

            if (!this.casesInfoForUpdate.length) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (!this.updateCasesDlgVisible) {
                // 
                this.updateCasesDlgVisible = true;
            }
        },
        // 
        newCase() {
            let executionPhase = [];
            for (let item in this.executedEachSprintDict) {
                if (this.executedEachSprintDict[item].default) {
                    executionPhase = [item];
                    break;
                }
            }

            this.caseData = {
                testplanId: this.planRow.id,
                suiteId: this.suiteID,
                productId: this.planRow.productId,
                sprintId: this.planRow.sprintId,
                suiteType: this.suiteType,
                customNo: "",
                name: "",
                createrName: "",
                updaterName: "",
                priority: "P3",
                executedEachSprint: "",
                executionMethod: "handwork",
                executionPhase: executionPhase,
                suitePath: this.suitePath,
                precondition: "",
                steps: [],
                postcondition: "",
                tags: [],
                desc: "",
                result: "",
                remark: "",
            };

            this.operation = "newCase";
            this.openCaseDialog("", true); // 
        },
        // 
        clickRow(row, column, event) {
            let handleFunc = () => {
                let counter = 0; // 
                let caseRowFound = false; // 
                for (let i = 0; i < this.tableData.length; i++) {
                    let testSuite = this.tableData[i];

                    if (testSuite.children.length) {
                        for (let i = 0; i < testSuite.children.length; i++) {
                            counter += 1;
                            let testCase = testSuite.children[i];
                            if (testCase.id == row.id) {
                                caseRowFound = true;
                                break;
                            }
                        }
                        if (caseRowFound) {
                            break;
                        }
                    }
                }
                this.caseOrder =
                    counter + (this.currentPage - 1) * this.pageSize;

                this.caseData = {
                    testplanId: row.testplanId,
                    suiteId: row.suiteId,
                    productId: row.productId,
                    sprintId: row.sprintId,
                    suiteType: row.suiteType,
                    detailSuiteType: row.detailSuiteType,
                    caseId: row.id,
                    guid: row.guid,
                    customNo: row.customNo,
                    name: row.name,
                    createrName: row.createrName,
                    updaterName: row.updaterName,
                    priority: row.priority,
                    executedEachSprint: row.executedEachSprint,
                    executionMethod: row.executionMethod,
                    executionPhase: row.executionPhase,
                    suitePath: row.suitePath,
                    precondition: row.precondition,
                    steps: JSON.parse(row.steps),
                    postcondition: row.postcondition,
                    desc: row.desc,
                    tags: row.tags,
                };

                this.operation = "viewCase";
                this.openCaseDialog("", false);
                this.row = row;
            };

            // ,
            if (!row.children.length && column && column.label == "") {
                if (this.caseDialogEditing) {
                    this.$confirm("?", "", {
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning",
                        cancelButtonClass: "btn-custom-cancel",
                    })
                        .then(() => {
                            handleFunc();
                        })
                        .catch(() => {});
                } else {
                    handleFunc();
                }
            }
        },
        // caseIDArray ID
        deleteCase(caseIDArray) {
            // 
            this.$api.testPlanCaseTable
                .deleteTestCases({
                    planId: this.planRow.id,
                    caseIds: caseIDArray,
                })
                .then((res) => {
                    if (res.success) {
                        this.$message.success(res.msg);
                        this.total = this.total - caseIDArray.length;

                        for (let i = 0; i < caseIDArray.length; i++) {
                            for (let x = 0; x < this.tableData.length; x++) {
                                // idID
                                let caseRowFound = false; // 
                                if (this.tableData[x].children.length) {
                                    // 
                                    for (
                                        let y = 0;
                                        y < this.tableData[x].children.length;
                                        y++
                                    ) {
                                        if (
                                            this.tableData[x].children[y].id ==
                                            caseIDArray[i]
                                        ) {
                                            caseRowFound = true;
                                            this.tableData[x].children.splice(
                                                y,
                                                1
                                            ); // 
                                            if (
                                                !this.tableData[x].children
                                                    .length
                                            ) {
                                                // 
                                                this.tableData.splice(x, 1);
                                            }

                                            break;
                                        }
                                    }
                                    if (caseRowFound) {
                                        //  // 
                                        break;
                                    }
                                }
                            }

                            // 
                            for (let key in res.data) {
                                if (key in this.planRow) {
                                    this.planRow[key] = res.data[key];
                                }
                            }

                            if (!this.tableData.length && this.total != 0) {
                                // 

                                let totalPages = Math.ceil(
                                    this.total / this.pageSize
                                );

                                if (this.currentPage > totalPages) {
                                    // 
                                    this.currentPage -= 1;
                                } else {
                                    // 
                                }

                                this.queryRows();
                                return;
                            }

                            // tableData()
                            this.tableData = Object.assign([], this.tableData);
                        }
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg);
                });
        },

        // 
        deleteOneCase(index, row) {
            if (this.showCaseDialog) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    // 
                    this.$api.testPlanCaseTable
                        .deleteTestCase({
                            id: row.id,
                            planId: row.testplanId,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.total = this.total - 1;

                                this.$message.success(res.msg);

                                for (
                                    let x = 0;
                                    x < this.tableData.length;
                                    x++
                                ) {
                                    let caseRowFound = false; // 
                                    if (this.tableData[x].children.length) {
                                        // 
                                        for (
                                            let y = 0;
                                            y <
                                            this.tableData[x].children.length;
                                            y++
                                        ) {
                                            if (
                                                this.tableData[x].children[y]
                                                    .guid == row.guid
                                            ) {
                                                caseRowFound = true;
                                                this.tableData[
                                                    x
                                                ].children.splice(y, 1); // 

                                                if (
                                                    !this.tableData[x].children
                                                        .length
                                                ) {
                                                    // 
                                                    this.tableData.splice(x, 1);
                                                }

                                                break;
                                            }
                                        }
                                        if (caseRowFound) {
                                            // 
                                            break;
                                        }
                                    }
                                }

                                // 
                                for (let key in res.data) {
                                    if (key in this.planRow) {
                                        this.planRow[key] = res.data[key];
                                    }
                                }

                                if (!this.tableData.length && this.total != 0) {
                                    // 

                                    let totalPages = Math.ceil(
                                        this.total / this.pageSize
                                    );

                                    if (this.currentPage > totalPages) {
                                        // 
                                        this.currentPage -= 1;
                                    } else {
                                        // 
                                    }

                                    this.queryRows();
                                    return;
                                }

                                // tableData(())
                                this.tableData = Object.assign(
                                    [],
                                    this.tableData
                                );
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg);
                        });
                })
                .catch(() => {});
        },
        //
        onSelectAllRow(selection) {
            // selection
            // tableData
            if (selection.indexOf(this.tableData[0]) > -1) {
                // 
                // 
                for (let i = 0; i < this.tableData.length; i++) {
                    for (
                        let j = 0;
                        j < this.tableData[i].children.length;
                        j++
                    ) {
                        this.$refs.myTable.toggleRowSelection(
                            this.tableData[i].children[j],
                            true
                        );
                    }
                }
            } else {
                // 
                this.$refs.myTable.clearSelection();
            }
        },
        //,
        onSelectRow(selection, row) {
            let selected;
            if (!selection.length) {
                selected = false; // 
            } else {
                for (let i = 0; i < selection.length; i++) {
                    if (selection[i].guid == row.guid) {
                        // 
                        selected = true;
                        break;
                    } else {
                        // 
                        selected = false;
                    }
                }
            }

            // \\
            if (row.children.length) {
                for (let j = 0; j < this.tableData.length; j++) {
                    if (this.tableData[j].guid == row.guid) {
                        for (
                            let i = 0;
                            i < this.tableData[j].children.length;
                            i++
                        ) {
                            this.$refs.myTable.toggleRowSelection(
                                this.tableData[j].children[i],
                                selected
                            );
                        }
                        break;
                    }
                }
            } else {
                // 
                // 
                for (let i = 0; i < this.tableData.length; i++) {
                    // 
                    let suiteRowFound = false;
                    for (
                        let j = 0;
                        j < this.tableData[i].children.length;
                        j++
                    ) {
                        if (this.tableData[i].children[j].guid == row.guid) {
                            suiteRowFound = true;
                            if (selected) {
                                // 
                                let suiteChecked = false;
                                for (let x = 0; x < selection.length; x++) {
                                    if (
                                        selection[x].guid ==
                                        this.tableData[i].guid
                                    ) {
                                        suiteChecked = true;
                                        break;
                                    }
                                }
                                if (!suiteChecked) {
                                    // 
                                    this.$refs.myTable.toggleRowSelection(
                                        this.tableData[i],
                                        true
                                    );
                                }
                            }
                            break;
                        }
                    }
                    if (suiteRowFound) {
                        break;
                    }
                }
            }
        },
        // 
        deleteCaseInBatch() {
            if (this.showCaseDialog) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    let rowsSelected = this.$refs.myTable.selection;
                    let caseIDArray = []; // ID

                    for (let i = 0; i < rowsSelected.length; i++) {
                        if (!rowsSelected[i].children.length) {
                            // 
                            caseIDArray.push(rowsSelected[i].id);
                        }
                    }

                    if (!caseIDArray.length) {
                        this.$alert("", "", {
                            confirmButtonText: "",
                        });
                        return;
                    }

                    this.deleteCase(caseIDArray);
                })
                .catch(() => {});
        },

        // 
        exportTestCase() {
            this.$alert("", "", {
                confirmButtonText: "",
            });

            return;
        },
        // 
        editRemark(index, row) {
            if (!this.remarkDialogVisible) {
                this.remarkDialogVisible = true;
                this.row = row;
            }
        },

        // 
        getUserOptionsList() {
            this.$api.sysUser
                .getUsersDetails()
                .then((res) => {
                    if (res.success) {
                        this.userOptions = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg);
                });
        },
        // 
        onAssignedToVisibleChange(event, row) {
            if (event) {
                // 
                this.assginedToOld = {
                    id: row.assignedToId,
                    name: row.assignedTo,
                };
            }
        },
        // 
        onAssignedToChanged(row) {
            let assignedToUserSelect = Object.assign({}, row.assignedTo);

            this.$api.testPlanCaseTable
                .updateTestCase({
                    id: row.id,
                    planId: row.testplanId,
                    assignedTo: row.assignedTo.name,
                    assignedToId: row.assignedTo.id,
                })
                .then((res) => {
                    if (res.success) {
                        this.$message.success(res.msg);

                        // 
                        for (let key in res.data) {
                            if (key in row) {
                                // key
                                row[key] = res.data[key];
                            }
                        }
                    } else {
                        // 
                        row["assignedTo"] = this.assginedToOld.assignedTo;
                        row["assignedToId"] = this.assginedToOld.assignedToId;
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    row["assignedTo"] = this.assginedToOld.name;
                    row["assignedToId"] = this.assginedToOld.id;
                    this.$message.error(res.msg);
                });
        },
        // 
        assignCases() {
            let rowsSelected = [];
            let selection = this.$refs.myTable.selection;
            let caseIDArray = []; // ID
            for (let i = 0; i < selection.length; i++) {
                if (!selection[i].children.length) {
                    // 
                    caseIDArray.push(selection[i].id);
                    rowsSelected.push(selection[i]);
                }
            }

            if (!caseIDArray.length) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (
                !this.batchAssignedTo ||
                JSON.stringify(this.batchAssignedTo) == "{}"
            ) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.$confirm(
                " " + this.batchAssignedTo.name + " ?",
                "",
                {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning",
                    cancelButtonClass: "btn-custom-cancel",
                }
            )
                .then(() => {
                    // 
                    this.$api.testPlanCaseTable
                        .updateTestCases({
                            caseIds: caseIDArray,
                            batchAssignedTo: this.batchAssignedTo,
                            planId: this.planRow.id,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message.success(res.msg);
                                // 
                                this.$nextTick(() => {
                                    const caseInfos = res.data;
                                    for (let i = 0; i < caseInfos.length; i++) {
                                        for (
                                            let j = 0;
                                            j < rowsSelected.length;
                                            j++
                                        ) {
                                            if (
                                                rowsSelected[j].id ==
                                                caseInfos[i].id
                                            ) {
                                                for (let key in caseInfos[i]) {
                                                    if (
                                                        key in
                                                            rowsSelected[j] &&
                                                        key != "id"
                                                    ) {
                                                        rowsSelected[j][key] =
                                                            caseInfos[i][key];
                                                    }
                                                }

                                                break;
                                            }
                                        }
                                    }
                                });
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg);
                        });
                })
                .catch(() => {});
        },
        // 
        sortChange(object) {
            let order = object.order;
            let property = object.prop;
            if (!property) {
                // 
                property = "-id";
            } else {
                property = this.$customUtils.stringHandler.humpToUnderline(
                    property
                );
                if (property == "create_time") {
                    property = "id";
                }
            }

            if (order == "descending") {
                if (property == "priority") {
                    this.queryFrom.sort = "-" + property + ",-id";
                } else {
                    this.queryFrom.sort = "-" + property;
                }
            } else {
                if (property == "priority") {
                    this.queryFrom.sort = property + ",id";
                } else {
                    this.queryFrom.sort = property;
                }
            }

            this.queryRows();
        },
        // 
        getTestPhases() {
            this.$api.testPhase
                .getTestPhasesDetails({
                    fields: "name,code,default",
                })
                .then((res) => {
                    if (res.success) {
                        for (let i = 0; i < res.data.length; i++) {
                            let label = res.data[i].name;
                            let value = res.data[i].code;
                            let defualt = res.data[i].default;
                            let temp = {
                                label: label,
                                value: value,
                                defualt: defualt,
                            };
                            this.executedEachSprintDict[value] = {};
                            this.executedEachSprintDict[value][
                                "executedEachSprint"
                            ] = "N";
                            this.executedEachSprintDict[value][
                                "executionPhase"
                            ] = res.data[i].name;
                            this.executedEachSprintDict[value][
                                "default"
                            ] = defualt;
                            this.executionPhaseOptions.push(temp);
                        }
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg);
                });
        },

        // 
        setTestResultStyle(index) {
            index += 1;
            if (this.$refs.myTable) {
                let testResult = this.$refs.myTable.$el.querySelector(
                    "tr:nth-child(" + index + ")"
                );

                if (testResult) {
                    let td = testResult.querySelector("td:nth-child(5)");
                    if (td) {
                        let testResultInputInner = td.querySelector(
                            ".el-input__inner"
                        );

                        let testResult = testResultInputInner.value;
                        if (testResult == "") {
                            testResultInputInner.style.cssText =
                                "font-weight:bold;color:green";
                        } else if (testResult == "") {
                            testResultInputInner.style.cssText =
                                "font-weight:bold;color:#B22222";
                        } else if (testResult == "") {
                            testResultInputInner.style.cssText =
                                "font-weight:bold;color:red";
                        } else {
                            testResultInputInner.style.cssText =
                                "font-weight:bold;color:black";
                        }
                    }
                }
            }
        },
        // 
        setTestResultsStyle() {
            if (this.$refs.myTable) {
                let testResults = this.$refs.myTable.$el.querySelectorAll(
                    "td:nth-child(5)"
                );
                testResults.forEach((element, index, srcArray) => {
                    let testResultInputInner = element.querySelector(
                        ".el-input__inner"
                    );

                    let testResult = testResultInputInner.value;
                    if (testResult == "") {
                        testResultInputInner.style.cssText =
                            "font-weight:bold;color:green";
                    } else if (testResult == "") {
                        testResultInputInner.style.cssText =
                            "font-weight:bold;color:#B22222";
                    } else if (testResult == "") {
                        testResultInputInner.style.cssText =
                            "font-weight:bold;color:red";
                    } else {
                        testResultInputInner.style.cssText =
                            "font-weight:bold;color:black";
                    }
                });
            }
        },

        // /
        toggleQueryItems() {
            this.isQueryItemsCollapsed = !this.isQueryItemsCollapsed;
        },
    },
    watch: {
        suiteID: function () {
            this.currentPage = 1;
            this.queryRows();
            this.caseDialogEditing = false;
            this.showCaseDialog = false;
            this.caseDialogLoaded = false;
        },
    },
    computed: {
        ...mapState({
            suiteType: (state) => state.sprintCaseSuite.suiteType,
            suiteID: (state) => state.sprintCaseSuite.suiteID,
            suitePath: (state) => state.sprintCaseSuite.suitePath,
            parentID: (state) => state.sprintCaseSuite.parentID,
        }),
        iscurrSuiteRoot: {
            get() {
                if (this.parentID == -1) {
                    return true;
                } else {
                    return false;
                }
            },
        },
    },
    created() {
        try {
            this.getUserOptionsList();
            this.getTestPhases();
        } catch (err) {
            this.$message.error(err.message);
        }
    },

    updated() {
        this.setTableBodySize(); // 

        this.expandAll(); // 
        this.$nextTick(() => {
            this.setTestResultsStyle();
        });
    },
};
</script>

<style lang="scss">
.my-table {
    .el-table__header-wrapper {
        th {
            padding-top: 1px !important;
            padding-bottom: 1px !important;
        }
    }

    // 
    .el-table__placeholder {
        width: 0px;
    }
    .el-table__indent {
        padding-left: 0px !important;
    }

    .row-class-for-colspan {
        background: rgb(231, 229, 229);
        td {
            padding-top: 1px !important;
            padding-bottom: 1px !important;
        }

        .cell {
            color: grey;
            height: auto !important;
            width: 100% !important;
        }
    }

    .row-class {
        td {
            padding-top: 3px !important;
            padding-bottom: 3px !important;
            // 
            .el-select,
            .el-input,
            .el-input__inner {
                height: auto !important;
            }
        }
        .cell {
            height: auto !important;
            padding-left: 2px !important;
            padding-right: 2px !important;
        }
    }
}
</style>



