<template>
    <el-dialog
        :title="dialogTitle"
        width="50%"
        :visible="dialogVisible"
        :show-close="showClose"
        :close-on-click-modal="closeOnClickModal"
        :close-on-press-escape="closeOnPressEscape"
        @close="onCloseDialog"
    >
        <div class="my-dialog-body">
            <el-form
                :model="dialogForm"
                :rules="rules"
                ref="dialogForm"
                :label-width="dialogFormLabeWidth"
            >
                <el-form-item label="" prop="type">
                    <el-radio-group v-model="dialogForm.type">
                        <el-radio
                            v-for="item in typeOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="dialogForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="dialogForm.desc" type="textarea"></el-input>
                </el-form-item>

                <el-form-item
                    label="URL"
                    prop="url"
                    v-if="(dialogForm.type == '') | (dialogForm.type == '')"
                >
                    <el-input v-model="dialogForm.url"></el-input>
                </el-form-item>
                <el-form-item label="" prop="icon">
                    <el-input v-model="dialogForm.icon"></el-input>
                </el-form-item>
                <el-form-item label="" prop="perms">
                    <el-input v-model="dialogForm.perms"></el-input>
                </el-form-item>
                <el-form-item label="" prop="requireAuth">
                    <el-radio-group v-model="dialogForm.requireAuth">
                        <el-radio
                            v-for="item in requireAuthOptions"
                            :key="item.value"
                            :label="item.value"
                        >{{ item.label }}</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="show" v-if="dialogForm.type != ''">
                    <el-radio-group v-model="dialogForm.show">
                        <el-radio
                            v-for="item in showOptions"
                            :key="item.value"
                            :label="item.value"
                        >{{ item.label }}</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="showWithoutAuth" v-if="dialogForm.type != ''">
                    <el-radio-group v-model="dialogForm.showWithoutAuth">
                        <el-radio
                            v-for="item in showOptions"
                            :key="item.value"
                            :label="item.value"
                        >{{ item.label }}</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="collapsed" v-if="dialogForm.type != ''">
                    <el-radio-group v-model="dialogForm.collapsed">
                        <el-radio
                            v-for="item in collapsedOptions"
                            :key="item.value"
                            :label="item.value"
                        >{{ item.label }}</el-radio>
                    </el-radio-group>
                </el-form-item>
                <!---->
                <el-form-item label="" prop="order" v-if="dialogForm.id">
                    <el-input v-model="dialogForm.order"></el-input>
                </el-form-item>
                <el-form-item class="dialog-footer-form-item">
                    <div class="my-dialog-footer">
                        <el-button @click="closeDialog"></el-button>
                        <el-button type="primary" @click="save('dialogForm')"></el-button>
                    </div>
                </el-form-item>
            </el-form>
        </div>
    </el-dialog>
</template>

<script>
import { elDialogMixin } from "@/common/mixins/elDialogMixin";

export default {
    props: ["dialogVisible", "dialogTitle", "dialogFormData", "menuNode"],
    mixins: [elDialogMixin],

    data() {
        return {
            dialogFormLabeWidth:'130px',
            dialogForm: Object.assign({}, this.dialogFormData),
            // 
            typeOptions: [
                { label: "", value: "" },
                { label: "", value: "" },
                { label: "", value: "" },
            ],
            requireAuthOptions: [
                { label: "", value: true },
                { label: "", value: false },
            ],
            showOptions: [
                { label: "", value: true },
                { label: "", value: false },
            ],
            collapsedOptions: [
                { label: "", value: true },
                { label: "", value: false },
            ],

            rules: {
                type: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                name: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                    {
                        min: 1,
                        max: 50,
                        message: " 1  50 ",
                        trigger: "blur",
                    },
                ],
                desc: [
                    {
                        min: 0,
                        max: 300,
                        message: " 300 ",
                        trigger: "blur",
                    },
                ],
                url: [
                    {
                        min: 0,
                        max: 100,
                        message: "url 100 ",
                        trigger: "blur",
                    },
                ],
                icon: [
                    {
                        min: 0,
                        max: 30,
                        message: " 30 ",
                        trigger: "blur",
                    },
                ],
                perms: [
                    {
                        min: 0,
                        max: 50,
                        message: " 50 ",
                        trigger: "blur",
                    },
                ],
                requireAuth: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                show: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                showWithoutAuth: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                collapsed: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    methods: {
        add() {
            const isLeafByUser = this.menuNode.isLeafByUser;
            this.menuNode.isLeafByUser = false; // 

            if (!this.menuNode.expanded) {
                // 
                this.$api.sysMenu
                    .getSysMenuTree({
                        parentId: this.menuNode.data.id,
                        recursive: false,
                    })
                    .then((res) => {
                        if (res.success) {
                            // 
                            if (!this.menuNode.childNodes) {
                                this.$set(this.menuNode, "childNodes", []);
                            }
                            this.$parent.$refs.sysMenuTree.updateKeyChildren(
                                this.menuNode.data.id,
                                res.data
                            );

                            // 
                            if (!this.menuNode.expanded) {
                                this.menuNode.expanded = true;
                            }

                            //  
                            this.$api.sysMenu
                                .addSysMenu(this.dialogForm)
                                .then((res) => {
                                    if (res.success) {
                                        let length = this.menuNode.childNodes
                                            .length;
                                        if (length) {
                                            // 
                                            this.$parent.$refs.sysMenuTree.insertAfter(
                                                res.data,
                                                this.menuNode.childNodes[
                                                    length - 1
                                                ]
                                            );
                                        } else {
                                            // 
                                            this.$parent.$refs.sysMenuTree.append(
                                                res.data,
                                                this.menuNode
                                            );
                                        }
                                        this.closeDialog();
                                        this.$message({
                                            message: res.msg,
                                            type: "success",
                                        });
                                    } else {
                                        // 
                                        if (isLeafByUser) {
                                            // 
                                            this.menuNode.isLeafByUser = true;
                                        }
                                        this.$message.error(res.msg);
                                    }
                                })
                                .catch((res) => {
                                    this.$message.error(res.msg || res.message);
                                });
                        } else {
                            this.$message.error(res.msg);
                        }
                    })
                    .catch((res) => {
                        this.$message.error(res.msg || res.message);
                    });
            } else {
                this.$api.sysMenu
                    .addSysMenu(this.dialogForm)
                    .then((res) => {
                        if (res.success) {
                            let length = this.menuNode.childNodes.length;
                            if (length) {
                                this.$parent.$refs.sysMenuTree.insertAfter(
                                    res.data,
                                    this.menuNode.childNodes[length - 1]
                                );
                            } else {
                                this.$parent.$refs.sysMenuTree.append(
                                    res.data,
                                    this.menuNode
                                );
                            }
                            this.closeDialog();
                            this.$message({
                                message: res.msg,
                                type: "success",
                            });
                        } else {
                            // 
                            if (isLeafByUser) {
                                // 
                                this.menuNode.isLeafByUser = true;
                            }
                            this.$message.error(res.msg);
                        }
                    })
                    .catch((res) => {
                        this.$message.error(res.msg || res.message);
                    });
            }
        },
        update() {
            this.$api.sysMenu
                .updateSysMenu(this.dialogForm)
                .then((res) => {
                    if (res.success) {
                        // 
                        for (let key in res.data) {
                            if (key in this.menuNode.data) {
                                // key
                                this.menuNode.data[key] = res.data[key];
                            }
                        }
                        this.closeDialog();
                        this.$message({
                            message: res.msg,
                            type: "success",
                        });
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // 
        save(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (!this.dialogForm.id) {
                        this.add();
                    } else {
                        this.update();
                    }
                } else {
                    // 
                }
            });
        },
    },
};
</script>

<style scoped>
.my-dialog-footer {
  text-align: center;
  margin-left: -130px;
}
</style>



