<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryForm" size="small">
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.name"
                        placeholder=""
                        clearable
                        style="width:240px"
                    ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="queryForm.status" clearable style="width: 100px;">
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :value="item.value"
                            :label="item.label"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.productOwner"
                        placeholder=""
                        clearable
                        style="width:230px"
                    ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="queryRows('clickQuery')"></el-button>
                </el-form-item>
            </el-form>
        </div>

        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button :size="buttonSize" @click="addProduct"></el-button>
                <el-button :size="buttonSize" @click="deleteRows($api.product.deleteProducts)"></el-button>
            </el-button-group>
        </div>

        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            border
            stripe
            :empty-text="emptyText"
            class="my-table"
            :cell-class-name="getCellClassName"
            @selection-change="onSelectChange"
            highlight-current-row
        >
            <!---->
            <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
            <el-table-column prop="id" label="ID" width="70px" header-align="center" align="center"></el-table-column>
            <el-table-column
                prop="name"
                label=""
                header-align="center"
                align="left"
                min-width="180px"
                show-overflow-tooltip
            ></el-table-column>
            <el-table-column
                prop="status"
                label=""
                width="50px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="productOwner"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="developOwner"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="testOwner"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="desc"
                label=""
                header-align="center"
                align="center"
                min-width="250px"
                show-overflow-tooltip
            ></el-table-column>
            <el-table-column
                prop="createrName"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updaterName"
                label=""
                width="95px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updateTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column label="" align="left" width="160px">
                <template slot-scope="scope">
                    <el-button size="mini" @click="editProduct(scope.$index, scope.row)"></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteRow(scope.$index, scope.row, $api.product.deleteProduct)"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!-- \ -->
        <product-dialog
            v-if="dialogVisible"
            :dialogVisible.sync="dialogVisible"
            :dialogTitle="dialogTitle"
            :tableData="tableData"
            :statusOptions="statusOptions"
            :row="row"
            :dialogFormData="dialogData"
        ></product-dialog>
    </div>
</template>

<script>
import ProductDialog from "./Product/ProductDialog";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

export default {
    mixins: [elTableMixin, elButtonMixin],
    data() {
        return {
            // 
            queryForm: {
                name: "", // 
                status: "", // 
                productOwner: "", // 
            },
            dialogData: null, // 
            statusOptions: [
                // 
                { label: "", value: "" },
                { label: "", value: "" },
            ],

            userName: "", // 
            dialogVisible: false, // \true -  false - 
            dialogTitle: "", // 
        };
    },
    components: {
        ProductDialog,
    },
    methods: {
        addProduct() {
            if (!this.dialogVisible) {
                this.dialogData = {
                    name: "", //
                    code: "", // 
                    status: "", // 
                    productOwner: { id: -1, name: "" }, // 
                    developOwner: { id: -1, name: "" }, // 
                    testOwner: { id: -1, name: "" }, // 
                    desc: "", // 
                };
                this.dialogVisible = true;
                this.dialogTitle = "";
            }
        },
        editProduct(index, row) {
            if (!this.dialogVisible) {
                this.dialogData = {
                    id: row.id,
                    name: row.name,
                    code: row.code,
                    status: row.status,
                    productOwner: {
                        id: row.productOwnerId,
                        name: row.productOwner,
                    },
                    developOwner: {
                        id: row.developOwnerId,
                        name: row.developOwner,
                    },
                    testOwner: { id: row.testOwnerId, name: row.testOwner },
                    desc: row.desc,
                };
                this.row = row;
                this.dialogVisible = true;
                this.dialogTitle = "";
            }
        },
    },
    updated() {
        this.setTableBodySize();
    },
    created() {
        this.queryRowsAPI = this.$api.product.getProducts;
        this.queryRows();
    },
};
</script>

<style lang="scss" scoped>
</style>



