<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryForm" size="small">
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.account"
                        placeholder=""
                        clearable
                        style="width:230px"
                    ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="queryForm.type" clearable style="width: 100px;">
                        <el-option
                            v-for="item in typeOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select
                        v-model="queryForm.isActive"
                        clearable
                        @clear="clearStatus"
                        style="width: 100px;"
                    >
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="queryRows('clickQuery')"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button :size="buttonSize" @click="addAccount"></el-button>
                <el-button
                    :size="buttonSize"
                    @click="deleteRows($api.msgPushAccount.deleteAccounts)"
                ></el-button>
            </el-button-group>
        </div>
        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            border
            stripe
            :empty-text="emptyText"
            :cell-class-name="getCellClassName"
            class="my-table"
            @selection-change="onSelectChange"
            highlight-current-row
        >
            <!---->
            <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
            <!-- <el-table-column prop="id" label="ID" width="70px" header-align="center" align="center"></el-table-column> -->
            <el-table-column
                prop="account"
                label=""
                header-align="center"
                align="left"
                min-width="200px"
            ></el-table-column>
            <el-table-column
                prop="type"
                label=""
                header-align="center"
                align="center"
                width="70px"
            ></el-table-column>
            <el-table-column
                prop="desc"
                label=""
                header-align="center"
                align="center"
                min-width="200px"
            ></el-table-column>
            <el-table-column
                prop="groups"
                label=""
                header-align="center"
                min-width="180px"
                align="left"
            >
                <template slot-scope="scope">
                    <span v-for="group in scope.row.groups" :key="group.id" @click.stop>
                        {{ group.name }}
                        <i
                            class="el-icon-close"
                            aria-hidden="true"
                            @click.prevent="deleteRelatedGroup(scope.row, group)"
                        >;&nbsp;&nbsp;</i>
                    </span>
                    <el-button size="mini" @click="bindGroup(scope.$index, scope.row)">
                        <i class="fa fa-plus" aria-hidden="true"></i>
                    </el-button>
                </template>
            </el-table-column>

            <el-table-column
                prop="status"
                label=""
                width="80px"
                header-align="center"
                align="center"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="scope.row.isActive"
                        size="mini"
                        @click="toggleAccountStatus(scope.$index, scope.row)"
                    ></el-button>
                    <el-button
                        v-else
                        size="mini"
                        @click="toggleAccountStatus(scope.$index, scope.row)"
                    ></el-button>
                </template>
            </el-table-column>
            <el-table-column
                prop="createrName"
                label=""
                width="100px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updaterName"
                label=""
                width="100px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updateTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>

            <el-table-column label="" align="left" width="160px">
                <template slot-scope="scope">
                    <el-button size="mini" @click="editAccount(scope.$index, scope.row)"></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteRow(scope.$index, scope.row, $api.msgPushAccount.deleteAccount)"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!-- \ -->
        <msg-push-account-dlg
            v-if="accountDialogVisible"
            :dialogVisible.sync="accountDialogVisible"
            :dialogTitle="accountDialogTitle"
            :tableData="tableData"
            :dialogFormData="accountData"
            :row="row"
            :typeOptions="typeOptions"
            :statusOptions="statusOptions"
        ></msg-push-account-dlg>

        <!--  -->
        <bind-sys-group-dialog
            v-if="bindGroupDialogVisible"
            :dialogVisible.sync="bindGroupDialogVisible"
            :row="row"
        ></bind-sys-group-dialog>
    </div>
</template>

<script>
import MsgPushAccountDlg from "./MsgPushAccount/MsgPushAccountDlg";
import BindSysGroupDialog from "./MsgPushAccount/BindSysGroupDialog";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

export default {
    mixins: [elTableMixin, elButtonMixin],
    data() {
        return {
            // 
            queryForm: {
                account: "", // 
                type: "", // 
                isActive: "", // 
            },
            accountData: null, // 
            accountDialogVisible: false, // \true -  false - 
            accountDialogTitle: "", // 

            bindGroupDialogVisible: false, // 
            statusOptions: [
                { label: "", value: true },
                { label: "", value: false },
            ],
            typeOptions: [
                { label: "", value: "" },
                { label: "", value: "" },
                { label: "", value: "" },
            ],
        };
    },
    components: {
        MsgPushAccountDlg,
        BindSysGroupDialog,
    },
    methods: {
        addAccount() {
            if (!this.accountDialogVisible) {
                this.accountData = {
                    account: "", //
                    type: "",
                    desc: "", // 
                    isActive: true, // 
                };

                this.accountDialogVisible = true;
                this.accountDialogTitle = "";
            }
        },
        editAccount(index, row) {
            if (!this.accountDialogVisible) {
                this.accountData = {
                    id: row.id,
                    account: row.account,
                    type: row.type,
                    desc: row.desc, // 
                    isActive: row.isActive, // 
                };

                this.row = row;
                this.accountDialogVisible = true;
                this.accountDialogTitle = "";
            }
        },
        // \
        toggleAccountStatus(index, row) {
            let targetStatus = row.isActive ? "" : "";
            this.$confirm("" + targetStatus + "?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.msgPushAccount
                        .toggleAccountStatus({
                            id: row.id,
                            isActive: !row.isActive,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                for (let key in res.data) {
                                    if (key in row) {
                                        // key
                                        row[key] = res.data[key];
                                    }
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        bindGroup(index, row) {
            if (!this.bindGroupDialogVisible) {
                this.bindGroupDialogVisible = true;
                this.row = row;
            }
        },
        deleteRelatedGroup(row, group) {
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.msgPushAccount
                        .deleteRelatedGroup({
                            accountId: row.id,
                            groupId: group.id,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                const index = row.groups.indexOf(group);
                                if (index != -1) {
                                    row.groups.splice(index, 1);
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        // isActive="",isActive
        clearStatus() {
            this.queryForm.isActive = "";
        },
    },
    updated() {
        this.setTableBodySize();
    },
    created() {
        this.queryRowsAPI = this.$api.msgPushAccount.getAccounts;
        this.queryRows();
    },
};
</script>

<style lang="scss">
</style>



