<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTabBodyHeight">
        <div class="test-info-wrapper" ref="topInfo">
            <span class="span-font"> / ID{{planRow.id}}</span>
            <span class="span-font">
                [&nbsp;
                <span class="test-info-text">{{planRow.status}}</span>&nbsp;&nbsp;
                <span
                    class="test-info-text"
                >{{Math.round(planRow.caseNumRelated ? (planRow.caseNumExecuted - planRow.caseNumBlocked)/planRow.caseNumRelated*100:0)}}%({{planRow.caseNumExecuted - planRow.caseNumBlocked}}/{{planRow.caseNumRelated}})</span>&nbsp;&nbsp;
                <span
                    class="test-info-text"
                >{{Math.round(planRow.caseNumRelated ? planRow.caseNumSuccess/planRow.caseNumRelated*100:0)}}%({{planRow.caseNumSuccess}}/{{planRow.caseNumRelated}})</span>&nbsp;&nbsp;<span
                    class="test-info-text"
                >{{Math.round(planRow.caseNumRelated ? planRow.caseNumFail/planRow.caseNumRelated*100:0)}}%({{planRow.caseNumFail}}/{{planRow.caseNumRelated}})</span>&nbsp;&nbsp;<span
                    class="test-info-text"
                >{{Math.round(planRow.caseNumRelated ? planRow.caseNumBlocked/planRow.caseNumRelated*100:0)}}%({{planRow.caseNumBlocked}}/{{planRow.caseNumRelated}})</span>&nbsp;&nbsp;<span
                    class="test-info-text"
                >{{planRow.caseNumRelated - planRow.caseNumExecuted}}</span>&nbsp;]
            </span>
            <!--/-->
            <span
                @click.prevent="togglePlanBaseInfo"
                style="height:32px; line-height: 32px;font-size:14px; color: #606266;"
            >
                <i class="el-icon-arrow-up el-icon--right" v-show="!planBaseInfoCollapsed"></i>
                <i class="el-icon-arrow-down el-icon--right" v-show="planBaseInfoCollapsed"></i>
            </span>
            <span class="span-font close-icon-span" @click.prevent="closePage">
                <i class="el-icon-back"></i>
            </span>
        </div>

        <!--  -->
        <div class="plan-base-info" ref="planBaseInfo" v-if="!planBaseInfoCollapsed">
            <table>
                <thead>
                    <tr>
                        <th style="border:none;width:30%;height:0px"></th>
                        <th style="border:none;width:15%;height:0px"></th>
                        <th style="border:none;width:20%;height:0px"></th>
                        <th style="border:none;width:20%;height:0px"></th>
                        <!-- <th style="border:none;width:10%;height:0px"></th> -->
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>
                            <label></label>
                            <div>{{planRow.name}}</div>
                        </td>
                        <td>
                            <label></label>
                            <div>{{planRow.createrName}}</div>
                        </td>

                        <td>
                            <label></label>
                            <div>{{planRow.beginTime}}</div>
                        </td>

                        <td>
                            <label></label>
                            <div>{{planRow.endTime}}</div>
                        </td>
                    </tr>

                    <tr>
                        <!-- <td>
                                <label></label>
                                <div
                                   
                                >{{planRow.sprint}}</div>
                        </td>-->
                        <td>
                            <label></label>
                            <div>{{planRow.envNames}}</div>
                        </td>
                        <td>
                            <label></label>
                            <div>{{planRow.createTime}}</div>
                        </td>
                        <td>
                            <label></label>
                            <div>{{planRow.startTime}}</div>
                        </td>
                        <td>
                            <label></label>
                            <div>{{planRow.finishTime}}</div>
                        </td>
                    </tr>
                    <tr>
                        <td colspan="2">
                            <label></label>
                            <div style="font-size:12px">{{planRow.desc}}</div>
                        </td>
                        <td colspan="2">
                            <label></label>
                            <div style="font-size:12px">{{planRow.projectNames}}</div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <div class="plan-related-tabs" ref="planRelatedTabs" v-resize="setTabBodyHeight">
            <!-- type="border-card"tabel-tabs__active-bar-->
            <el-tabs
                v-model="defaultActiveTabPaneName"
                @tab-click="onTabClicked"
                type="border-card"
            >
                <el-tab-pane label="" name="caseList">
                    <test-plan-case-list-tab
                        v-if="displayTestPlanCaseListTab"
                        v-show="showPlanRelatedTabs"
                        :sprintID="planRow.sprintId"
                        :planRow="planRow"
                        ref="testPlanCaseListTab"
                    ></test-plan-case-list-tab>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script>
import TestPlanCaseListTab from "./TestPlanCaseListTab";

export default {
    props: ["planRow", "planDetailVisible"],
    components: {
        TestPlanCaseListTab,
    },
    data() {
        return {
            defaultActiveTabPaneName: "caseList", // tab
            displayTestPlanCaseListTab: true, // tab
            planBaseInfoCollapsed: true, // 
            showPlanRelatedTabs: true, // tab
        };
    },
    mounted() {
        this.$nextTick(() => {
            this.setTabBodyHeight(); // tab body
        });
    },
    methods: {
        // tab body
        setTabBodyHeight() {
            let viewWrapper = this.$refs.viewWrapper;
            let topInfo = this.$refs.topInfo;
            let planBaseInfo = this.$refs.planBaseInfo;
            let planBaseInfoOffsetHeight = 0;
            let planRelatedTabs = this.$refs.planRelatedTabs;

            if (planBaseInfo) {
                planBaseInfoOffsetHeight = planBaseInfo.offsetHeight;
            }

            if (planRelatedTabs && topInfo && planRelatedTabs) {
                let h =
                    viewWrapper.offsetHeight -
                    topInfo.offsetHeight -
                    planBaseInfoOffsetHeight -
                    31 - // 31 plan-related-tabs
                    10 - // 10 margin 
                    30; // el-tabs__content padding

                planRelatedTabs.getElementsByClassName(
                    "el-tabs__content"
                )[0].style.height = h + "px";
            }
        },
        closePage() {
            this.$emit("update:planDetailVisible", false); // 
        },
        // 
        onTabClicked(tab, event) {
            this.displayTestPlanCaseListTab = true;
        },
        // /
        togglePlanBaseInfo() {
            this.planBaseInfoCollapsed = !this.planBaseInfoCollapsed;
            this.showPlanRelatedTabs = false;
            this.$nextTick(() => {
                this.showPlanRelatedTabs = true;
                this.setTabBodyHeight();
                this.$refs.testPlanCaseListTab.$refs.rightElement.setTableBodySize();
            });
        },
    },
};
</script>

<style lang="scss">
.test-info-wrapper {
    margin: 5px 0px 5px 0px;
    line-height: 30px;
    height: 30px;
    background: #f2f3f7;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(172, 167, 167, 0.3);
    .close-icon-span {
        float: right;
        margin-right: 5px;
    }
}
.test-info-text {
    color: #00a8b3;
}

/* */
.plan-base-info {
    border-style: solid;
    border-width: 1px;
    border-color: rgba(172, 167, 167, 0.3);
    background: rgb(241, 239, 239);
    td {
        padding-top: 5px;
        padding-bottom: 5px;
        label {
            font-size: 14px;
            color: #606266;
        }
        div {
            display: inline;
            font-size: 14px;
        }
    }
}
</style>


