<template>
    <div :id="elementID"></div>
</template>

<script>
import uuidv1 from "uuid/v1"; // uuid
export default {
    name: "doughnut-pie-chart",
    props: [
        "pieData",
        "pieColor",
        "pieTitle",
        "seriesRadius",
        "hideLegendValue",
        "seriesLabelFormatter",
        "tooltipFormatter",
        "echartPicIdDict",
        "keyForEchartId",
        "showLabel",
        "seriesCenter"
    ],
    data() {
        return {
            elementID: "", // ID
            color: [], // 
            legendData: [], // 
            centerTextLeft: 0, // 
            centerTextTop: 0 // 
        };
    },
    methods: {
        // 
        getTextPixelWith(text, fontStyle) {
            var canvas = document.createElement("canvas"); //  canvas 
            var context = canvas.getContext("2d"); //  canvas 
            context.font = fontStyle; //  font 
            var dimension = context.measureText(text); // 
            return dimension.width;
        }
    },
    created() {
        this.elementID = uuidv1(); //id
        this.echartPicIdDict[this.keyForEchartId] = this.elementID;

        if (this.pieColor) {
            // 
            for (let i = 0; i < this.pieData.seriesData.length; i++) {
                this.legendData.push({
                    name: this.pieData.seriesData[i].name,
                    // 
                    textStyle: {
                        color: this.pieColor[this.pieData.seriesData[i].name]
                    }
                });

                // 
                this.color.push(this.pieColor[this.pieData.seriesData[i].name]);
            }
        } else {
            // 
            for (let i = 0; i < this.pieData.seriesData.length; i++) {
                this.legendData.push({
                    name: this.pieData.seriesData[i].name
                });
            }
        }
    },

    mounted() {
        // 
        let pieContainer = document.getElementById(this.elementID);
        let pieContainerWidth = window.getComputedStyle(pieContainer).width;
        pieContainerWidth = parseFloat(pieContainerWidth.split("px")[0]);
        let pieContainerHeight = window.getComputedStyle(pieContainer).height;
        pieContainerHeight = parseFloat(pieContainerHeight.split("px")[0]);

        let centerTextFontSize = 15; // 
        let centerTextFontFamily = "Microsoft YaHei"; // family

        // 
        let lines = this.pieData.centerText.split("\n");
        let max_line_pixel_width = 0;
        for (let i = 0; i < lines.length; i++) {
            // 
            let textLinePixelWidth = this.getTextPixelWith(
                lines[i],
                centerTextFontSize + "px " + '"' + centerTextFontFamily + '"'
            );
            if (max_line_pixel_width < textLinePixelWidth) {
                max_line_pixel_width = textLinePixelWidth;
            }
        }

        // 
        // x -  / 2
        let centerX = 0.4; // x
        let centerY = 0.6; // y
        if (this.seriesCenter) {
            let x = this.seriesCenter[0];
            let tempIndex = x.indexOf("%");
            if (tempIndex != -1) {
                centerX = parseInt(x.substring(0, tempIndex)) / 100.0;
            } else {
                centerX = parseInt(x) / 100.0;
            }
            let y = this.seriesCenter[1];
            tempIndex = y.indexOf("%");
            if (tempIndex != -1) {
                centerY = parseInt(y.substring(0, tempIndex)) / 100.0;
            } else {
                centerY = parseInt(y) / 100.0;
            }
        }
        this.centerTextLeft =
            pieContainerWidth * centerX - max_line_pixel_width / 2;
        // topy -  *  / 2  //fontSize
        this.centerTextTop =
            pieContainerHeight * centerY - (centerTextFontSize * lines.length) / 2;

        let option = {
            title: {
                // 
                text: this.pieTitle, //
                textStyle: {
                    fontSize: "15" // 
                    // color: "grey" // 
                },
                left: "3%", // 
                top: "5%" // 
            },
            // color // 
            // 
            // ['#c23531','#2f4554', '#61a0a8', '#d48265', '#91c7ae','#749f83',  '#ca8622', '#bda29a','#6e7074', '#546570', '#c4ccd3']
            // color: this.color,
            // 
            tooltip: {
                trigger: "item", //  item 
                // formatter     
                formatter: this.tooltipFormatter || "{a} <br/>{b}: {c} ({d}%)"
            },
            graphic: [
                {
                    //
                    type: "text",
                    left: this.centerTextLeft, // lefttopcenternumber
                    top: this.centerTextTop,
                    style: {
                        text: this.pieData.centerText, // 
                        textAlign: "center", // 
                        textVerticalAlign: "middle", // 
                        fill: "#000", //
                        fontSize: centerTextFontSize, // 
                        fontFamily: centerTextFontFamily
                    }
                }
            ],

            legend: {
                orient: "vertical", // 
                // icon: 'circle', // 
                itemHeight: 0, // 
                itemWidth: 0, // 
                top: "3%", // 
                right: "0", // 
                x: "right", // 
                // 
                formatter: name => {
                    let target = undefined;
                    let percent = undefined;
                    for (let i = 0; i < this.pieData.seriesData.length; i++) {
                        if (this.pieData.seriesData[i].name == name) {
                            target = this.pieData.seriesData[i].value;
                            percent = this.pieData.seriesData[i].percent;
                            break;
                        }
                    }

                    if (this.hideLegendValue) {
                        target = "";
                    }

                    if (!percent) {
                        return "{a|" + name + "} " + target;
                    }
                    // let arr = [target, "{a|" + name + "}"];
                    // if (target && percent) {
                    //     arr = [
                    //         target,
                    //         "(" + percent + "%)",
                    //         "{a|" + name + "}"
                    //     ];
                    // } else if (target && !percent) {
                    //     arr = [target, "{a|" + name + "}"];
                    // }
                    let arr = ["{a|" + name + "}", target];
                    if (target && percent) {
                        arr = [
                            "{a|" + name + "}",
                            target,
                            "(" + percent + "%)"
                        ];
                    } else if (target && !percent) {
                        arr = ["{a|" + name + "}", target];
                    }
                    return arr.join(" ");
                },
                data: this.legendData,
                textStyle: {
                    rich: {
                        a: {
                            color: "black" //
                        }
                    },
                    fontSize: 15
                }
            },

            series: [
                {
                    name: this.pieTitle, //  //tooltiplegend  setOption 
                    type: "pie", // 
                    center: this.seriesCenter || ["40%", "60%"], // 
                    radius: this.seriesRadius || ["50%", "60%"], // 
                    avoidLabelOverlap: true, //  false
                    label: {
                        // 
                        normal: {
                            show: this.showLabel, // true -false-
                            position: "outside", // 
                            formatter:
                                this.seriesLabelFormatter || "{b} {c} ({d}%)", // 
                            fontSize: centerTextFontSize //  //
                        }
                    },
                    // 
                    labelLine: {
                        normal: {
                            show: true
                        }
                    },
                    data: this.pieData.seriesData, // 
                    enableMouseTracking: false,
                    shadow: false,
                    animation: false
                }
            ]
        };
        if (this.pieData.aroundTextConfig) {
            for (let i = 0; i < this.pieData.aroundTextConfig.length; i++) {
                option.graphic.push(this.pieData.aroundTextConfig[i]);
            }
        }

        if (this.color.length) {
            option.color = this.color;
        }

        const chartObj = this.$echarts.init(
            document.getElementById(this.elementID)
        );

        chartObj.setOption(option);

        window.addEventListener("resize", function() {
            chartObj.resize();
        });
    }
};
</script>



