<template>
    <el-dialog
        :title="dialogTitle"
        width="40%"
        :visible="dialogVisible"
        :show-close="showClose"
        :close-on-click-modal="closeOnClickModal"
        :close-on-press-escape="closeOnPressEscape"
        @close="onCloseDialog"
    >
        <div class="my-dialog-body">
            <el-form :model="dialogForm" :rules="rules" ref="dialogForm" label-width="120px">
                <el-form-item label="" prop="name">
                    <el-input v-model="dialogForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="productId">
                    <el-select
                        :disabled="dialogForm.projectId != null"
                        v-model="dialogForm.productId"
                        :multiple="false"
                        clearable
                        filterable
                        style="width:100%"
                        placeholder=""
                    >
                        <el-option
                            v-for="item in productOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="beginTime">
                    <el-date-picker
                        v-model="dialogForm.beginTime"
                        type="date"
                        placeholder=""
                        value-format="yyyy-MM-dd"
                        style="width:100%"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="endTime">
                    <el-date-picker
                        v-model="dialogForm.endTime"
                        type="date"
                        placeholder=""
                        value-format="yyyy-MM-dd"
                        style="width:100%"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="status">
                    <el-select v-model="dialogForm.status" placeholder="" style="width:100%">
                        <el-option
                            v-for="item in this.statusOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="dialogForm.desc" type="textarea"></el-input>
                </el-form-item>
                <el-form-item class="dialog-footer-form-item">
                    <div class="my-dialog-footer">
                        <el-button @click="closeDialog"></el-button>
                        <el-button type="primary" @click="save('dialogForm')"></el-button>
                    </div>
                </el-form-item>
            </el-form>
        </div>
    </el-dialog>
</template>

<script>
import { elDialogMixin } from "@/common/mixins/elDialogMixin";

export default {
    props: [
        "dialogVisible",
        "dialogTitle",
        "dialogFormData",
        "tableData",
        "statusOptions",
        "row",
    ],
    mixins: [elDialogMixin],
    data() {
        return {
            dialogForm: Object.assign({}, this.dialogFormData),
            productOptions: [], // 
            rules: {
                name: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                    {
                        min: 1,
                        max: 50,
                        message: " 1  50 ",
                        trigger: "blur",
                    },
                ],
                productId: [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ],
                beginTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ],
                endTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ],
                status: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                desc: [
                    {
                        min: 0,
                        max: 300,
                        message: " 300 ",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    methods: {
        // 
        save(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    const beginTime = new Date(
                        this.dialogForm.beginTime.replace(/-/g, "/")
                    ).getTime();

                    const endTime = new Date(
                        this.dialogForm.endTime.replace(/-/g, "/")
                    ).getTime();

                    if (beginTime > endTime) {
                        this.$message.error("");
                        return;
                    }

                    if (!this.dialogForm.id) {
                        this.add(this.$api.project.addProject);
                    } else {
                        this.update(this.$api.project.updateProject);
                    }
                } else {
                    // 
                }
            });
        },
    },
    created() {
        // 
        this.$api.product
            .getProductsDetails({ fields: "id,name" })
            .then((res) => {
                if (res.success) {
                    this.productOptions = res.data;
                } else {
                    this.$message.error(res.msg);
                }
            })
            .catch((res) => {
                this.$message.error(res.msg || res.message);
            });
    },
};
</script>

<style scoped>
</style>



