<template>
    <el-dialog
        title=""
        :visible="dialogVisible"
        :show-close="showClose"
        :close-on-click-modal="true"
        :close-on-press-escape="closeOnPressEscape"
        :append-to-body="true"
        custom-class="batch-update-cases-dlg-class"
        @close="onCloseDialog"
    >
        <div class="my-dialog-body">
            <div class="remark-text">
                
                <br />1
                <br />2
            </div>
            <el-form
                :model="dialogForm"
                :rules="rules"
                ref="dialogForm"
                label-width="70px"
                class="case-info-form"
                size="mini"
            >
                <el-form-item label="" prop="priority">
                    <el-select v-model="dialogForm.priority" style="width: 100px;" size="mini">
                        <el-option
                            v-for="item in priorityOptions"
                            :key="item"
                            :label="item"
                            :value="item"
                        ></el-option>
                    </el-select>
                    <el-checkbox class="participation-check-box" v-model="priorityChecked"></el-checkbox>
                </el-form-item>
                <el-form-item label="" prop="executionMethod">
                    <el-select
                        v-model="dialogForm.executionMethod"
                        style="width: 100px;"
                        size="mini"
                    >
                        <el-option
                            v-for="item in executionMethodOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                    <el-checkbox
                        class="participation-check-box"
                        v-model="executionMethodChecked"
                    ></el-checkbox>
                </el-form-item>
                <el-form-item label="" prop="executionPhase">
                    <el-select
                        v-model="dialogForm.executionPhase"
                        style="width: 70%;"
                        size="mini"
                        multiple
                    >
                        <el-option
                            v-for="item in executionPhaseOptions"
                            :key="item.value"
                            :label="
                item.label +
                  '/' +
                  executedEachSprintMap[item.value]['executedEachSprint']
              "
                            :value="item.value"
                        >
                            
                            <span
                                style="float: right; color: #8492a6; font-size: 13px;margin-right:50px; "
                            >
                                <span></span>
                                <el-radio
                                    v-model="
                    executedEachSprintMap[item.value]['executedEachSprint']
                  "
                                    label="Y"
                                    @click.native="clickRadio(item, 'Y')"
                                ></el-radio>
                                <el-radio
                                    v-model="
                    executedEachSprintMap[item.value]['executedEachSprint']
                  "
                                    label="N"
                                    @click.native="clickRadio(item, 'N')"
                                ></el-radio>
                            </span>
                        </el-option>
                    </el-select>
                    <el-checkbox
                        class="participation-check-box"
                        v-model="executionPhaseChecked"
                    ></el-checkbox>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <div>
                        <el-input
                            type="textarea"
                            :autosize="{ minRows: 3 }"
                            ref="descInput"
                            placeholder="()"
                            v-model="dialogForm.desc"
                            maxlength="1000"
                            show-word-limit
                        ></el-input>
                        <el-checkbox class="participation-check-box" v-model="descChecked"></el-checkbox>
                    </div>
                </el-form-item>
                <el-form-item label="" prop="preconditionInput">
                    <el-input
                        ref="preconditionInput"
                        type="textarea"
                        :autosize="{ minRows: 3 }"
                        placeholder="()"
                        v-model="dialogForm.precondition"
                        maxlength="1000"
                        show-word-limit
                    ></el-input>
                    <el-checkbox
                        class="participation-check-box"
                        v-model="preconditionChecked"
                    ></el-checkbox>
                </el-form-item>

                <el-form-item label="" prop="postcondition">
                    <el-input
                        style="white-space: pre-wrap; word-wrap: break-word;"
                        ref="postconditionInput"
                        type="textarea"
                        :autosize="{ minRows: 3 }"
                        placeholder="()"
                        v-model="dialogForm.postcondition"
                        maxlength="1000"
                        show-word-limit
                    ></el-input>
                    <el-checkbox
                        class="participation-check-box"
                        v-model="postconditionChecked"
                    ></el-checkbox>
                </el-form-item>
                <el-form-item label="" prop="tags">
                    <el-tag
                        :key="tag"
                        v-for="tag in dialogForm.tags"
                        :closable="true"
                        :disable-transitions="true"
                        @close="onCloseTag(tag)"
                    >{{ tag }}</el-tag>
                    <el-input
                        class="input-new-tag"
                        v-if="tagInputVisible"
                        v-model="tagInputValue"
                        ref="saveTagInput"
                        size="small"
                        @keyup.enter.native="handleTagInputConfirm"
                        @blur="handleTagInputConfirm"
                    ></el-input>

                    <el-button class="button-new-tag" size="small" @click="showTagInput">+</el-button>
                    <el-checkbox class="participation-check-box" v-model="tagsChecked"></el-checkbox>
                    <div class="custom-form-text"></div>
                </el-form-item>
                <el-form-item class="dialog-footer-form-item">
                    <!-- <div class="my-dialog-footer">
                        <el-button @click="closeDialog" style></el-button>
                        <el-button type="primary" @click="save('dialogForm')" style></el-button>
                    </div>-->
                </el-form-item>
            </el-form>
        </div>
        <div slot="footer">
            <el-button @click="closeDialog"></el-button>
            <el-button type="primary" @click="save('dialogForm')"></el-button>
        </div>
    </el-dialog>
</template>

<script>
import { elDialogMixin } from "@/common/mixins/elDialogMixin";

export default {
    props: [
        "dialogVisible",
        "executionPhaseOptions",
        "executedEachSprintDict",
        "casesInfoForUpdate",
        "suiteId",
    ],
    mixins: [elDialogMixin],
    data() {
        return {
            dialogForm: {
                priority: "P3", //
                executionMethod: "handwork", // 
                executionPhase: ["SystemTesting"], //
                desc: "", // 
                precondition: "", //
                postcondition: "", // 
                tags: [], // 
            },
            priorityChecked: true, // 
            priorityOptions: ["p1", "P2", "P3", "P4"],
            executionMethodOptions: [
                { label: "", value: "handwork" },
                { label: "", value: "automation" },
            ],
            executionMethodChecked: false, // 
            executionPhaseChecked: false, // 
            descChecked: false, // 
            preconditionChecked: false, // 
            postconditionChecked: false, //
            tagsChecked: false, // 
            tagInputVisible: false, //  false  true 
            tagInputValue: "", // 
            executedEachSprintMap: {},
            rules: {
                desc: [
                    {
                        min: 0,
                        max: 1000,
                        message: " 1000 ",
                        trigger: "blur",
                    },
                ],
                precondition: [
                    {
                        min: 0,
                        max: 1000,
                        message: " 1000 ",
                        trigger: "blur",
                    },
                ],
                postcondition: [
                    {
                        min: 0,
                        max: 1000,
                        message: " 1000 ",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    methods: {
        //radio
        clickRadio(item, value) {
            this.executedEachSprintMap[item.value][
                "executedEachSprint"
            ] = value;
        },

        // tag
        onCloseTag(tag) {
            this.dialogForm.tags.splice(this.caseInfo.tags.indexOf(tag), 1);
        },
        // tag
        showTagInput() {
            this.tagInputVisible = true;
            this.$nextTick((_) => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        // tag
        handleTagInputConfirm() {
            let tagInputValue = this.tagInputValue;
            // tags
            if (
                tagInputValue &&
                this.dialogForm.tags.indexOf(tagInputValue) == -1
            ) {
                this.dialogForm.tags.push(tagInputValue);
            }
            this.tagInputVisible = false;
            this.tagInputValue = "";
        },
        // 
        caseInfoCheck() {
            if (this.priorityChecked && !this.dialogForm.priority) {
                this.$message.error("");
                return false;
            }

            if (
                this.executionMethodChecked &&
                !this.dialogForm.executionMethod
            ) {
                this.$message.error("");
                return false;
            }

            if (
                this.executionPhaseChecked &&
                !this.dialogForm.executionPhase.length
            ) {
                this.$message.error("");
                return false;
            }

            return true;
        },
        // 
        save(formName) {
            try {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (!this.caseInfoCheck()) {
                            return;
                        }
                        let caseInfo = {};
                        if (this.priorityChecked) {
                            caseInfo["priority"] = this.dialogForm.priority;
                        }

                        if (this.executionMethodChecked) {
                            caseInfo[
                                "executionMethod"
                            ] = this.dialogForm.executionMethod;
                        }
                        if (this.executionPhaseChecked) {
                            caseInfo[
                                "executionPhase"
                            ] = this.dialogForm.executionPhase;

                            let tempStr = "";
                            for (
                                let i = 0;
                                i < caseInfo["executionPhase"].length;
                                i++
                            ) {
                                let executionPhase =
                                    caseInfo["executionPhase"][i];
                                // this.executedEachSprintMap keyexecutionPhase
                                if (
                                    executionPhase in this.executedEachSprintMap
                                ) {
                                    tempStr +=
                                        executionPhase +
                                        "/" +
                                        this.executedEachSprintMap[
                                            executionPhase
                                        ]["executedEachSprint"] +
                                        ",";
                                }
                            }
                            caseInfo["executedEachSprint"] = tempStr.substring(
                                0,
                                tempStr.length - 1
                            );

                            caseInfo["executionPhase"] = caseInfo[
                                "executionPhase"
                            ].join(",");
                        }
                        if (this.descChecked) {
                            caseInfo["desc"] = this.dialogForm.desc;
                        }
                        if (this.preconditionChecked) {
                            caseInfo[
                                "precondition"
                            ] = this.dialogForm.precondition;
                        }
                        if (this.postconditionChecked) {
                            caseInfo[
                                "postcondition"
                            ] = this.dialogForm.postcondition;
                        }
                        if (this.tagsChecked) {
                            caseInfo["tags"] = this.dialogForm.tags;
                            caseInfo["tags"] = caseInfo["tags"].join(",");
                        }

                        caseInfo["targetCases"] = this.casesInfoForUpdate;
                        caseInfo["suiteId"] = this.suiteId;

                        // 
                        this.$parent.loading = true;
                        this.$api.sprintCaseTable
                            .batachUpdateTestCases(caseInfo)
                            .then((res) => {
                                if (res.success) {
                                    this.$message.success(res.msg);
                                } else {
                                    this.$message.error(res.msg);
                                }
                                this.$parent.loading = false;
                                this.$parent.queryRows();
                            })
                            .catch((res) => {
                                this.$parent.loading = false;
                                this.$message.error(res.msg || res.message);
                                this.$parent.queryRows();
                            });
                        this.closeDialog();
                    }
                });
            } catch (err) {
                this.$parent.loading = false;
                this.$message.error(err.message);
            }
        },
    },
    created() {
        this.executedEachSprintMap = JSON.parse(
            JSON.stringify(this.executedEachSprintDict)
        );
    },
};
</script>
<style lang="scss">
.batch-update-cases-dlg-class {
    position: fixed;
    display: block;
    height: 100%;
    width: 50%;
    left: 50%;
    // right: 0px; // 
    margin-top: 0px !important;
    overflow: auto;
}
/**/
@keyframes dialog-fade-in {
    0% {
        transform: translate3d(100%, 0, 0);
        opacity: 0;
    }
    100% {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes dialog-fade-out {
    0% {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
    100% {
        transform: translate3d(100%, 0, 0);
        opacity: 0;
    }
}
</style>
<style scoped lang="scss" scoped>
.my-dialog-body .el-form-item {
    margin-right: 10px;
}

.remark-text {
    margin-left: 0px;
    color: red;
}

.participation-check-box {
    margin-left: 10px;
}

// textarea
/deep/ .el-textarea {
    width: 70%;
}

// 
.input-new-tag {
    width: 90px;
    vertical-align: bottom;
}

// 
.custom-form-text {
    margin-left: -69px;
}
</style>



