<template>
  <div class="testplan-case-detail-content-div">
    <el-container>
      <!--  -->
      <el-header>
        <keep-alive>
          <header-bar :currentModule="currentModule"></header-bar>
        </keep-alive>
      </el-header>

      <!--  -->
      <div class="plan-case-detail-content">
        <div class="case-detail-div">
          <el-button size="small" @click="editCase" v-if="!caseEditing">
            <i class="fa fa-edit" aria-hidden="true"></i>
          </el-button>
          <el-button size="small" @click="cancelCaseEditing" v-else>
            <i class="fa fa-edit" aria-hidden="true"></i>
          </el-button>
          <el-button size="small" @click="saveCase" v-if="caseEditing">
            <i class="fa fa-floppy-o" aria-hidden="true"></i>
          </el-button>
        </div>
        <div class="case-detail-div">
          <span></span>
          <span v-if="!caseEditing" style="font-weight:bold;">{{
            caseInfo.customNo
          }}</span>
          <el-input
            v-else
            placeholder=""
            v-model="caseInfo.customNo"
            minlength="1"
            maxlength="60"
            size="small"
            clearable
          ></el-input>

          <br />
          <span></span>
          <span v-if="!caseEditing" style="font-weight:bold;">{{
            caseInfo.name
          }}</span>

          <el-input
            v-else
            placeholder=""
            v-model="caseInfo.name"
            minlength="1"
            maxlength="60"
            size="small"
            clearable
          ></el-input>
        </div>
        <div class="case-detail-div">
          <div style="display:inline-block; margin-right:20px">
            <span>{{ caseInfo.createrName }}</span>
          </div>
          <div style="display:inline-block; margin-right:20px">
            <span>{{ caseInfo.updaterName }}</span>
          </div>

          <div
            style="display:inline-block; padding-left: 20px;padding-right:20px"
          >
            <span></span>
            <span v-if="!caseEditing">{{ caseInfo.priority }}</span>
            <el-select
              v-else
              ref="casePrioritySelect"
              v-model="caseInfo.priority"
              clearable
              style="width: 100px;"
              size="mini"
            >
              <el-option label="P1" value="P1"></el-option>
              <el-option label="P2" value="P2"></el-option>
              <el-option label="P3" value="P3"></el-option>
              <el-option label="P4" value="P4"></el-option>
            </el-select>
          </div>
          <div
            style="display:inline-block; padding-left: 20px;padding-right:40px"
          >
            <span></span>
            <span v-if="!caseEditing">{{ caseInfo.type }}</span>
            <el-select
              v-else
              ref="typeSelect"
              v-model="caseInfo.type"
              clearable
              style="width: 100px;"
              size="mini"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </div>
          <div style="display:inline-block">
            <span>{{ caseInfo.suitePath }}</span>
          </div>
        </div>

        <div class="case-detail-div">
          <span></span>
          <span v-if="!caseEditing">{{ caseInfo.precondition }}</span>
          <el-input
            v-else
            ref="preconditionInput"
            type="textarea"
            placeholder=""
            v-model="caseInfo.precondition"
            maxlength="30"
            show-word-limit
          ></el-input>
        </div>

        <div class="case-detail-div">
          
          <!--  -->
          <el-row v-if="caseEditing">
            <el-button-group>
              <el-button size="mini" @click="addCaseStep"></el-button>
              <el-button size="mini" @click="deleteCaseStepsInBatch"
                ></el-button
              >
            </el-button-group>
            <span></span>
          </el-row>

          <!--  -->
          <el-table
            :data="caseStepTableData"
            style="width: 100%;margin-bottom: 0px; overflow:auto"
            border
            @row-dblclick="dblclickStepRow"
            @selection-change="onSelectChange"
          >
            <!---->
            <el-table-column
              type="selection"
              width="35px"
              align="center"
              :resizable="false"
            ></el-table-column>
            <el-table-column
              type="index"
              align="center"
              :resizable="false"
            ></el-table-column>
            <!--  -->
            <el-table-column
              prop="id"
              label="ID"
              width="65px"
              header-align="center"
              align="center"
              :resizable="false"
            ></el-table-column>
            <el-table-column
              prop="action"
              label=""
              min-width="250px"
              show-overflow-tooltip
              header-align="center"
              align="left"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.editing">
                  <el-input
                    type="textarea"
                    size="small"
                    placeholder=""
                    v-model="scope.row.action"
                  ></el-input>
                </span>
                <span v-else>{{ scope.row.action }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="expection"
              label=""
              min-width="250px"
              header-align="center"
              align="center"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.editing">
                  <el-input
                    type="textarea"
                    size="small"
                    placeholder=""
                    v-model="scope.row.expection"
                  ></el-input>
                </span>
                <span v-else>{{ scope.row.expection }}</span>
              </template>
            </el-table-column>

            <el-table-column
              label=""
              align="center"
              width="220px"
              :resizable="false"
              v-if="caseEditing"
            >
              <template slot-scope="scope">
                <el-button
                  v-if="!scope.row.editing"
                  size="mini"
                  @click="editCaseStep(scope.$index, scope.row)"
                  ></el-button
                >
                <el-button
                  v-if="scope.row.editing"
                  size="mini"
                  @click="finishCaseStepEditing(scope.$index, scope.row)"
                  ></el-button
                >
                <el-button
                  v-if="scope.row.editing"
                  size="mini"
                  @click="cancelCaseStepEditing(scope.$index, scope.row)"
                  ></el-button
                >
                <el-button
                  v-if="!scope.row.editing"
                  size="mini"
                  @click="insertCaseStep(scope.$index, scope.row)"
                  ></el-button
                >

                <el-button
                  size="mini"
                  type="danger"
                  @click="deleteOneCaseStep(scope.$index, scope.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>

        <div class="case-detail-div">
          <span></span>
          <span v-if="!caseEditing">{{ caseInfo.postcondition }}</span>
          <el-input
            v-else
            ref="postconditionInput"
            type="textarea"
            placeholder=""
            v-model="caseInfo.postcondition"
            maxlength="30"
            show-word-limit
          ></el-input>
        </div>

        <div class="case-detail-div">
          <span style="margin-right:10px"></span>
          <el-tag
            :key="tag"
            v-for="tag in this.caseInfo.tags"
            :closable="caseEditing"
            :disable-transitions="true"
            @close="onCloseTag(tag)"
            >{{ tag }}</el-tag
          >
          <el-input
            class="input-new-tag"
            v-if="tagInputVisible"
            v-model="tagInputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleTagInputConfirm"
            @blur="handleTagInputConfirm"
          ></el-input>

          <el-button
            v-else-if="caseEditing"
            class="button-new-tag"
            size="small"
            @click="showTagInput"
            >+</el-button
          >
        </div>
        <div class="case-detail-div">
          <span></span>
          <span v-if="!caseEditing" style="padding-right:20px">{{
            caseInfo.desc
          }}</span>
          <el-input
            v-else
            type="textarea"
            ref="descInput"
            placeholder=""
            v-model="caseInfo.desc"
            maxlength="1000"
            show-word-limit
          ></el-input>
        </div>
        <div class="case-detail-div">
          <el-upload
            class="upload-demo"
            :action="urlForCaseAttachmentUpload"
            :on-success="onAttachmentUploadSuccess"
            :on-error="onAttachmentUploadError"
            :before-upload="beforeAttachmentUpload"
            :file-list="caseAttachmentList"
            :on-remove="onRemoveCaseAttachment"
            :limit="caseAttachmentNumlimit"
            :on-exceed="onAttachmentUploadNumExceed"
            :with-credentials="true"
          >
            <el-button size="small" type="primary"></el-button>
            <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
          </el-upload>
        </div>

        <div
          class="case-detail-div"
          v-if="!caseEditing && currentModule != 'testPlanCaseTable'"
        >
          <el-button size="mini">
            &nbsp;
            <i class="fa fa-link" aria-hidden="true"></i>
          </el-button>
        </div>
      </div>

      <!--  -->
      <el-footer>
        <keep-alive>
          <Footer></Footer>
        </keep-alive>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import HeaderBar from "@/views/HeaderBar/HeaderBar";
import Footer from "@/views/Footer/Footer";
import constant from "@/common/constant";

export default {
  data() {
    return {
      planID: 0, // ID
      caseCustomNo: "", // 
      currentModule: "testPlanCaseDetail", // 
      caseEditing: false, // 
      caseInfo: {},
      caseStepTableData: [], // 
      caseStepOeration: "viewCaseStep", //  deleteCaseStep newCaseStep editCaseStep
      stepRowData: [], // ()
      caseStepRowSelected: [], // 
      caseStepIDsDeleted: [], // ()ID
      tagInputVisible: false, //  false  true 
      tagInputValue: "", // 
      urlForCaseAttachmentUpload: constant.urlForCaseAttachmentUpload, // 
      caseAttachmentNumlimit: constant.caseAttachmentNumlimit, // 
      caseAttachmentList: [] // 
    };
  },
  components: {
    HeaderBar,

    Footer
  },
  methods: {
    // 
    editCase() {
      if (!this.caseEditing) {
        this.caseEditing = true;
      }
    },
    // 
    cancelCaseEditing() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        cancelButtonClass: "btn-custom-cancel"
      })
        .then(() => {
          // 
          this.caseEditing = false;
        })
        .catch(() => {});
    },
    // 
    caseInfoCheck() {
      if (!this.caseInfo.name) {
        this.$message.error("");
        return false;
      }
      return true;
    },
    // 
    saveCase() {
      this.$confirm("?", "", {
        type: "info"
      })
        .then(() => {
          if (!this.caseInfoCheck()) {
            return;
          }

          this.caseInfo["suiteType"] = "testPlan";
          // 
          this.$api.sprintCaseDialog
            .updateTestCase({
              caseInfo: this.caseInfo,
              caseSteps: this.caseStepTableData,
              attachmentList: this.caseAttachmentList
            })
            .then(res => {
              if (res.success) {
                this.$message({
                  message: res.msg,
                  type: "success"
                });
                // false
                this.caseEditing = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .catch(res => {
              this.$message.error("" + res.msg || res.message);
            });
        })
        .catch(() => {});
    },
    // 
    dblclickStepRow(row, column, event) {
      this.editCaseStep(null, row);
    },
    // 
    editCaseStep(index, row) {
      if (!row.editing && this.caseEditing) {
        this.stepRowData = Object.assign({}, row);
        row.editing = true;
        this.caseStepOeration = "editCaseStep";
      }
    },
    // ""
    finishCaseStepEditing(index, row) {
      if (row.editing) {
        row.editing = false;
      }
    }, // 
    cancelCaseStepEditing(index, row) {
      if (row.editing) {
        row.editing = false;
      }

      if (this.caseStepOeration == "newCaseStep") {
        // 
        this.caseStepTableData.splice(index, 1);
        // 
      } else if (this.caseStepOeration == "editCaseStep") {
        for (let key in row) {
          if (key in this.stepRowData) row[key] = this.stepRowData[key];
        }
      }
    },
    // 
    // caseStep 
    // index 
    addStep(index, caseStep) {
      this.caseStepOeration = "newCaseStep";

      this.caseStepTableData.splice(index, 0, caseStep);
    },
    // 
    addCaseStep() {
      let caseStep = {
        action: "",
        expection: "",
        editing: true
      };

      this.addStep(this.caseStepTableData.length, caseStep);
    },
    // 
    insertCaseStep(index, row) {
      let caseStep = {
        action: "",
        expection: "",
        editing: true
      };
      this.addStep(index + 1, caseStep);
    },
    // 
    deleteOneCaseStep(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        cancelButtonClass: "btn-custom-cancel"
      })
        .then(() => {
          this.caseStepTableData.splice(index, 1);

          if (row.id) {
            this.caseStepIDsDeleted.push(row.id);
          }
        })
        .catch(() => {});
    },
    // 
    onSelectChange(selection) {
      this.caseStepRowSelected = selection;
    },
    // 
    deleteCaseStepsInBatch() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        cancelButtonClass: "btn-custom-cancel"
      })
        .then(() => {
          if (!this.caseStepRowSelected.length) {
            this.$alert("", "", {
              confirmButtonText: ""
            });
            return;
          }

          for (let i = 0; i < this.caseStepRowSelected.length; i++) {
            for (let x = 0; x < this.caseStepTableData.length; x++) {
              if (this.caseStepTableData[x] == this.caseStepRowSelected[i]) {
                this.caseStepTableData.splice(x, 1);
                if (this.caseStepRowSelected[i].id) {
                  this.caseStepIDsDeleted.push(this.caseStepRowSelected[i].id);
                }
              }
            }
          }
        })
        .catch(() => {});
    },
    // tag
    onCloseTag(tag) {
      this.caseInfo.tags.splice(this.caseInfo.tags.indexOf(tag), 1);
    },
    // tag
    showTagInput() {
      this.tagInputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // tag
    handleTagInputConfirm() {
      let tagInputValue = this.tagInputValue;
      // tags
      if (tagInputValue && this.caseInfo.tags.indexOf(tagInputValue) == -1) {
        this.caseInfo.tags.push(tagInputValue);
      }
      this.tagInputVisible = false;
      this.tagInputValue = "";
    },
    // 
    beforeAttachmentUpload(file) {
      if (!file.size) {
        // file.size
        this.$message({
          message: "",
          type: "warning"
        });
        return false;
      }
      return true;
    },
    // 
    onAttachmentUploadSuccess(response, file, fileList) {
      for (let i = 0; i < fileList.length; i++) {
        if (fileList[i].uid == file.uid) {
          fileList.splice(i, 1);
          break;
        }
      }

      if (response.success) {
        this.$message({
          message: response.msg,
          type: "success"
        });
        this.caseAttachmentList.push(response.data);
        //   
        if (!this.caseEditing) {
          this.caseEditing = true;
        }
      } else {
        this.caseAttachmentList = fileList;
        this.$message.error(response.msg);
      }
    },
    // 
    onAttachmentUploadError(err, file, fileList) {
      this.$message.error("");
      console.log(err);
    },
    // 
    onAttachmentUploadNumExceed(files, fileList) {
      this.$message.error(
        "" + this.caseAttachmentNumlimit + ""
      );
    },
    // 
    onRemoveCaseAttachment(file, fileList) {
      this.caseAttachmentList = fileList;

      if (!this.caseEditing) {
        //   
        this.caseEditing = true;
      }
    }
  },
  created() {
    // IDurl
    if (JSON.stringify(this.$route.query) == "{}") {
      this.$message({
        message: "url",
        type: "error"
      });
      return;
    } else {
      this.planID = this.$route.query.planID;
      this.caseCustomNo = this.$route.query.caseCustomNo;

      if (!this.planID) {
        this.$message({
          message: "url(planID)",
          type: "error"
        });
        return;
      }

      if (!this.caseCustomNo) {
        this.$message({
          message: "url(caseCustomNo)",
          type: "error"
        });
        return;
      }
    }

    // 
    this.$api.testPlanBugTable
      .getPlanRelatedCase({
        planID: this.planID,
        caseCustomNo: this.caseCustomNo
      })
      .then(res => {
        if (res.success) {
          this.caseInfo = res.data;
        } else {
          this.$message.error(res.msg);
        }
      })
      .catch(res => {
        this.$message.error("" + res.msg || res.message);
      });
  }
};
</script>

<style scoped>
.testplan-case-detail-content-div {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  overflow: hidden;
}

.plan-case-detail-content {
  margin: 10px 10px 10px 10px;
  position: absolute;
  top: 60px;
  left: 0px;
  right: 0px;
  bottom: 40px;
  overflow-y: auto;
}

.case-detail-div {
  border-style: solid;
  border-width: 1px;
  border-style: solid;
  background: rgba(241, 239, 239, 0.438);
  border-color: rgb(204, 206, 206);
  margin: 5px 0px 5px 0px;
  padding: 10px 0px 10px 0px;
}

/*  */
.case-step-table {
  overflow: "auto";
}

/*  */
.el-tag {
  margin-right: 10px;
}

/*  */
.button-new-tag {
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}

/*  */
.input-new-tag {
  width: 90px;
  vertical-align: bottom;
}
</style>



