<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <div v-if="!planDetailVisible">
            <!--  -->
            <div ref="tableToolbar" class="table-toolbar">
                <el-button-group>
                    <el-button :size="buttonSize" @click="newPlan"></el-button>
                    <el-button
                        :size="buttonSize"
                        @click="deleteRows($api.sprintTestPlan.deleteTestPlans)"
                    ></el-button>
                </el-button-group>
            </div>
            <!--  -->
            <div class="table-query-form" ref="queryForm">
                <el-form :inline="true" :model="queryForm" size="small">
                    <el-form-item label="">
                        <el-date-picker
                            style="width:330px"
                            v-model="createTime"
                            type="datetimerange"
                            placeholder=""
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            size="mini"
                            :default-time="['00:00:00', '23:59:59']"
                            value-format="yyyy-MM-dd HH:mm:ss"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker
                            style="width:330px"
                            v-model="finishTime"
                            type="datetimerange"
                            placeholder=""
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            size="mini"
                            :default-time="['00:00:00', '23:59:59']"
                            value-format="yyyy-MM-dd HH:mm:ss"
                        ></el-date-picker>
                    </el-form-item>

                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.name"
                            placeholder=""
                            clearable
                            style="width:230px"
                        ></el-input>
                    </el-form-item>

                    <el-form-item label="">
                        <el-select
                            v-model="queryForm.status"
                            :multiple="true"
                            clearable
                            style="width: 235px;"
                        >
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="queryForm.sprintId" clearable>
                            <el-option
                                v-for="item in sprintOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.creater"
                            placeholder=""
                            clearable
                            style="width:200px"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.environment"
                            placeholder=""
                            clearable
                            style="width:150px"
                        ></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="queryRows('clickQuery')"></el-button>
                    </el-form-item>
                </el-form>
            </div>

            <!--  -->
            <el-table
                ref="myTable"
                :data="tableData"
                border
                stripe
                :empty-text="emptyText"
                class="my-table testplan-table"
                :lazy="false"
                @selection-change="onSelectChange"
                @row-click="clickRow"
                highlight-current-row
            >
                <!---->
                <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
                <!-- <el-table-column prop="id" label="ID" width="60px" header-align="center" align="center"></el-table-column> -->
                <el-table-column
                    prop="name"
                    label=""
                    show-overflow-tooltip
                    header-align="center"
                    min-width="220px"
                    align="left"
                >
                    <template slot-scope="scope">
                        <el-link
                            type="primary"
                            @click="viewPlanDetail(scope.$index, scope.row)"
                        >{{ scope.row.name }}</el-link>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="sprintName"
                    label=""
                    show-overflow-tooltip
                    width="120px"
                    header-align="center"
                    align="center"
                ></el-table-column>
                <el-table-column
                    prop="projectNames"
                    label=""
                    show-overflow-tooltip
                    width="180px"
                    min-width="100"
                    header-align="center"
                    align="center"
                ></el-table-column>
                <el-table-column
                    prop="createrName"
                    label=""
                    width="90px"
                    header-align="center"
                    align="center"
                ></el-table-column>
                <el-table-column
                    prop="createTime"
                    label=""
                    width="150px"
                    header-align="center"
                    align="center"
                ></el-table-column>
                <el-table-column
                    prop="finishTime"
                    label=""
                    width="150px"
                    header-align="center"
                    align="center"
                ></el-table-column>
                <el-table-column
                    prop="envNames"
                    label=""
                    width="100px"
                    header-align="center"
                    show-overflow-tooltip
                    align="center"
                ></el-table-column>
                <el-table-column
                    prop="progress"
                    label=""
                    width="120px"
                    header-align="center"
                    align="center"
                >
                    <template
                        slot-scope="scope"
                    >{{Math.round(scope.row.caseNumRelated ? (scope.row.caseNumExecuted - scope.row.caseNumBlocked)/scope.row.caseNumRelated*100:0)}}%({{scope.row.caseNumExecuted - scope.row.caseNumBlocked}}/{{scope.row.caseNumRelated}})</template>
                </el-table-column>
                <el-table-column
                    prop="passRate"
                    label=""
                    width="120px"
                    header-align="center"
                    align="center"
                >
                    <template
                        slot-scope="scope"
                    >{{Math.round(scope.row.caseNumRelated ? scope.row.caseNumSuccess/scope.row.caseNumRelated*100:0)}}%({{scope.row.caseNumSuccess}}/{{scope.row.caseNumRelated}})</template>
                </el-table-column>
                <el-table-column
                    prop="status"
                    label=""
                    width="60px"
                    header-align="center"
                    align="center"
                ></el-table-column>
                <el-table-column label="" align="left" width="350px">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="bindCase(scope.$index, scope.row)"></el-button>
                        <el-button size="mini" @click="viewPlanDetail(scope.$index, scope.row)"></el-button>
                        <el-button size="mini" @click="editPlan(scope.$index, scope.row)"></el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="deleteRow(scope.$index, scope.row, $api.sprintTestPlan.deleteTestPlan)"
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination
                ref="tablePagination"
                class="table-pagination"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                layout="total, sizes, prev, pager, next"
                :hide-on-single-page="false"
                :total="total"
                prev-text=""
                next-text=""
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pageSize"
                :pager-count="pagerCount"
            ></el-pagination>

            <!--  -->
            <test-plan-dialog
                v-if="planDialogVisible"
                :dialogVisible.sync="planDialogVisible"
                :dialogTitle="planDialogTitle"
                :tableData="tableData"
                :dialogFormData="planData"
                :row="row"
                :sprintOptions="sprintOptions"
            ></test-plan-dialog>

            <!--  -->
            <bind-case-dialog
                v-if="bindCaseDialogVisible"
                :dialogVisible.sync="bindCaseDialogVisible"
                :planRow="row"
            ></bind-case-dialog>
        </div>

        <!--  -->
        <test-plan-detail
            :planRow="row"
            v-if="planDetailVisible"
            :planDetailVisible.sync="planDetailVisible"
        ></test-plan-detail>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

import TestPlanDialog from "./TestPlanDialog";
import BindCaseDialog from "./BindCaseDialog";
import TestPlanDetail from "./TestPlanDetail";

export default {
    mixins: [elTableMixin, elButtonMixin],
    props: ["productID"],
    data() {
        return {
            // 
            queryForm: {
                startTime: "", // 
                endTime: "", // 
                finishStartTime: "", // 
                finishEndTime: "", // 
                name: "", // 
                status: [], // ,
                creater: "", // 
                envId: "", // 
                sprintId: "", // ID
                productId: this.productID, // ID
            },
            pageSizes: [10, 15, 20, 30, 40, 50, 100, 150], //
            pageSize: 15, // 
            planData: null, // 

            planDialogVisible: false, // \true -  false - 
            planDialogTitle: "", // 

            bindCaseDialogVisible: false, //  true -  false - 
            sprintCaseIDList: [], // sprintID(ID)
            baseCaseIDList: [], // ID(ID)
            planDetailVisible: false, //  true -  false - 

            tabPanelName: "", // tab
            sprintOptions: [], // 
        };
    },
    components: {
        TestPlanDialog,
        BindCaseDialog,
        TestPlanDetail,
    },
    methods: {
        // 
        queryRows(action) {
            if (action == "clickQuery") {
                this.currentPage = 1;
            }

            let queryParams = Object.assign({}, this.queryForm);
            queryParams["pageSize"] = this.pageSize;
            queryParams["pageNo"] = this.currentPage;
            queryParams.status = queryParams.status.join(",");

            if (!this.productID) {
                this.total = 0;
                this.tableData = [];
                return;
            }

            // 
            this.$api.sprintTestPlan
                .getTestPlans(queryParams)
                .then((res) => {
                    if (res.success) {
                        this.total = res.data.total;
                        this.tableData = res.data.rows;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg);
                });
        },
        // 
        newPlan() {
            if (!this.planDialogVisible) {
                this.planData = {
                    name: "", // 
                    desc: "", // 
                    beginTime: "", // 
                    endTime: "", // 
                    sprint: this.sprintOptions.length
                        ? this.sprintOptions[0]
                        : null, // 
                    projectIds: [], // id
                    envNames: [], // 
                    sprintId: null, // ID
                    productId: this.productID, // ID
                };
                this.planDialogVisible = true;
                this.planDialogTitle = "";
            }
        },
        // 
        editPlan(index, row) {
            if (!this.planDialogVisible) {
                this.getTestPlanDetail(row.id)
                    .then((data) => {
                        for (let key in row) {
                            row[key] = data[key];
                        }

                        let projectIdList = row.projectIds.split(",");
                        let projectIds = projectIdList.map((item) => {
                            return parseInt(item);
                        });

                        this.planData = {
                            id: row.id,
                            name: row.name, // 
                            desc: row.desc, // 
                            beginTime: row.beginTime, // 
                            endTime: row.endTime, // 
                            sprint: {
                                // 
                                id: row.sprintId,
                                name: row.sprintName,
                            },
                            projectIds: projectIds, // id
                            productId: row.productId,
                            envNames: row.envNames.split(","), // 
                        };
                        this.row = row;
                        this.planDialogVisible = true;
                        this.planDialogTitle = "";
                    })
                    .catch((err) => {
                        this.$message.error(err.msg||err.message);
                    });
            }
        },
        // 
        bindCase(index, row) {
            if (!this.bindCaseDialogVisible) {
                this.bindCaseDialogVisible = true;
                this.row = row;
            }
        },

        // 
        viewPlanDetail(index, row) {
            if (!this.planDetailVisible) {
                this.planDetailVisible = true;
                this.row = row;
            }
        },
        // tab
        removePlanDetailTab() {
            this.planDetailVisible = false;
        },
        // 
        clickRow(row, column, event) {
            // if (column && column.label != "") {
            //     this.viewPlanDetail(undefined, row);
            // }
        },
        // 
        getProductSprintsDetails() {
            if (!this.productID) {
                this.sprintOptions = [];
                return;
            }
            this.$api.product
                .getProductSprintsDetails({
                    productId: this.productID,
                })
                .then((res) => {
                    if (res.success) {
                        this.sprintOptions = res.data;
                    } else {
                        this.sprintOptions = [];
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg);
                    this.sprintOptions = [];
                });
        },
        // 
        getTestPlanDetail(planId) {
            return new Promise((resolve, reject) => {
                this.$api.sprintTestPlan
                    .getTestPlanDetail({
                        id: planId,
                    })
                    .then((res) => {
                        if (res.success) {
                            resolve(res.data);
                        } else {
                            reject(res);
                        }
                    })
                    .catch((res) => {
                        reject(res);
                    });
            });
        },
    },
    watch: {
        sprint: function () {
            this.planDetailVisible = false; // 
            this.currentPage = 1;
            this.queryRows();
        },
    },
    computed: {
        ...mapState({
            suiteType: (state) => state.sprintCaseSuite.suiteType,
            suiteID: (state) => state.sprintCaseSuite.suiteID,
        }),
        createTime: {
            get() {
                if (this.queryForm.startTime && this.queryForm.endTime) {
                    return [this.queryForm.startTime, this.queryForm.endTime];
                } else {
                    return [];
                }
            },
            set(value) {
                if (value) {
                    this.queryForm.startTime = value[0];
                    this.queryForm.endTime = value[1];
                } else {
                    this.queryForm.startTime = "";
                    this.queryForm.endTime = "";
                }
            },
        },
        finishTime: {
            get() {
                if (
                    this.queryForm.finishStartTime &&
                    this.queryForm.finishEndTime
                ) {
                    return [
                        this.queryForm.finishStartTime,
                        this.queryForm.finishEndTime,
                    ];
                } else {
                    return [];
                }
            },
            set(value) {
                if (value) {
                    this.queryForm.finishStartTime = value[0];
                    this.queryForm.finishEndTime = value[1];
                } else {
                    this.queryForm.finishStartTime = "";
                    this.queryForm.finishEndTime = "";
                }
            },
        },
    },
    created() {
        this.queryRows(); // 
        this.getProductSprintsDetails();
    },
    updated() {
        this.setTableBodySize(); // 
    },
};
</script>



<style lang="scss">
.testplan-table {
    .cell {
        padding-left: 5px !important;
        padding-right: 5px !important;
    }

    .operation-col-cell-class .cell .el-button--danger {
        padding-left: 15px !important;
        padding-right: 15px !important;
    }
}
</style>





