<template>
  <div class="system-menu-div">
    <el-tree
      ref="sysMenuTree"
      class="system-menu-tree"
      :props="defaultProps"
      :load="loadNode"
      empty-text=""
      lazy
      node-key="id"
      :expand-on-click-node="false"
      :render-content="renderContent"
      :default-expanded-keys="defaultExpandedKeys"
    ></el-tree>

    <sys-menu-dialog
      v-if="menuDialogVisible"
      :dialogVisible.sync="menuDialogVisible"
      :dialogTitle="menuDialogTitle"
      :dialogFormData="menuData"
      :menuNode="menuNode"
    ></sys-menu-dialog>
  </div>
</template>

<script>
import SysMenuDialog from "./SysMenu/SysMenuDialog";


export default {
  components: {
    SysMenuDialog
  },
  data() {
    return {
      defaultProps: {
        label: "name",
        children: "children",
        isLeaf: "isLeaf" // 
      },
      defaultExpandedKeys: [], // ID
      menuDialogVisible: false, //  true-false-
      menuDialogTitle: "", // 
      menuData: null, // 
      menuNode: null // 
    };
  },

  methods: {
    loadNode(node, resolve) {
      // loadNodemounted
      if (node.level == 0) {
        this.$api.sysMenu
          .getSysMenuTree({ parentId: -1, recursive: false })
          .then(res => {
            if (res.success) {
              //
              for (let i = 0; i < res.data.length; i++) {
                this.defaultExpandedKeys.push(res.data[i].id);
              }

              return resolve(res.data);
            } else {
              this.$message.error(res.msg);
              return resolve([]);
            }
          })
          .catch(res => {
            this.$message.error(res.msg || res.message);
            return resolve([]);
          });
      } else if (node.level >= 1) {
        // id
        this.$api.sysMenu
          .getSysMenuTree({
            parentId: node.data.id,
            recursive: false
          })
          .then(res => {
            if (res.success) {
              return resolve(res.data);
            } else {
              this.$message.error(res.msg);
              return resolve([]);
            }
          })
          .catch(res => {
            this.$message.error(res.msg || res.message);
            return resolve([]);
          });
      }
    },
    // 
    addNode(node, data, store) {
      if (!this.menuDialogVisible) {
        this.menuDialogVisible = true;
        this.menuDialogTitle = "";

        this.menuData = {
          parentId: data.id,
          type: "",
          name: "",
          desc: "",
          name: "",
          url: "",
          icon: "",
          perms: "",
          requireAuth: true,
          show: true,
          showWithoutAuth: true,
          collapsed: false,
          order: ""
        };

        this.menuNode = node;
      }
    },
    // 
    editNode(node, data, store) {
      if (!this.menuDialogVisible) {
        this.menuDialogVisible = true;
        this.menuDialogTitle = "";

        this.menuData = {
          id: data.id,
          type: data.type,
          name: data.name,
          desc: data.desc,
          url: data.url,
          icon: data.icon,
          perms: data.perms,
          requireAuth: data.requireAuth,
          show: data.show,
          showWithoutAuth: data.showWithoutAuth,
          collapsed: data.collapsed,
          order: data.order
        };

        this.menuNode = node;
      }
    },
    removeNode(node, data, store) {
      // 
      this.$confirm(
        "' ",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          cancelButtonClass: "btn-custom-cancel"
        }
      )
        .then(() => {
          // 
          this.$api.sysMenu
            .deleteSysMenu({
              id: data.id
            })
            .then(res => {
              if (res.success) {
                this.$refs.sysMenuTree.remove(node);
                this.$message({
                  message: res.msg,
                  type: "success"
                });
              } else {
                this.$message.error(res.msg);
              }
            })
            .catch(res => {
              this.$message.error(res.msg || res.message);
            });
        })
        .catch(() => {});
    },
    renderContent(h, { node, data, store }) {
      return h(
        "span",
        {
          style: {
            // color: "red"
          },
          //hover
          on: {
            // 
            mouseenter: () => {
              this.$set(data, "showOperationBtn", true);
              this.$set(data, "children", []);
            },
            //
            mouseleave: () => {
              this.$set(data, "showOperationBtn", false);
            }
          }
        },
        [
          h(
            "span",
            {
              //
            },
            node.label
          ),
          h(
            "span",
            {
              style: {
                display: data.showOperationBtn ? "" : "none"
              }
            },
            [
              h(
                "el-button",
                {
                  props: {
                    type: "text",
                    size: "small"
                  },
                  style: {
                    marginLeft: "10px"
                  },
                  on: {
                    click: () => {
                      this.addNode(node, data, store);
                    }
                  }
                },
                ""
              ),
              h(
                "el-button",
                {
                  props: {
                    type: "text",
                    size: "small"
                  },
                  style: {
                    display: data.id ? "" : "none" //  
                  },
                  on: {
                    click: () => {
                      this.editNode(node, data, store);
                    }
                  }
                },
                ""
              ),
              h(
                "el-button",
                {
                  props: {
                    type: "text",
                    size: "small"
                  },
                  style: {
                    display: data.id ? "" : "none"
                  },
                  on: {
                    click: () => {
                      this.removeNode(node, data, store);
                    }
                  }
                },
                ""
              )
            ]
          )
        ]
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.system-menu-div {
  height: 100%;
}

.system-menu-tree {
  position: absolute;
  top: 10px;
  bottom: 0px;
  left: 10px;
  width: 30%;
  overflow: auto;
  font-size: 14px;
  background: #e7e6e6;
  border-color: rgb(204, 206, 206);
  border-style: solid;
  border-width: 1px;
}
</style>



