<template>
    <div :id="elementID" style="height: 0px"></div>
</template>

<script>
import uuidv1 from "uuid/v1"; // uuid
export default {
    name: "stacked-column-chart",
    props: [
        "barData",
        "barColor",
        "barTitle",
        "echartPicIdDict",
        "keyForEchartId",
        "yAxisType",
        "xAxisType",
    ],
    data() {
        return {
            elementID: "", // ID
            color: [], // 
            legendData: [], // 
            axisYType: this.yAxisType, // y
            axisXType: this.xAxisType, // x
        };
    },
    created() {
        this.elementID = uuidv1(); //id
        this.echartPicIdDict[this.keyForEchartId] = this.elementID;
        // // 
        for (let i = 0; i < this.barData.seriesData.length; i++) {
            this.legendData.push(this.barData.seriesData[i].name);

            if (this.barColor) {
                // 
                this.color.push(this.barColor[this.barData.seriesData[i].name]);
            }
        }
    },
    mounted() {
        if (!this.axisXType) {
            this.axisXType = "category";
        }

        if (!this.axisYType) {
            this.axisYType = "value";
        }

        let option = {
            title: {
                text: this.barTitle, //
                textStyle: {
                    fontSize: "14", // 
                    color: "grey", // 
                },
                left: "3%", // 
                top: "5%", // 
            },
            // color: this.color,
            tooltip: {
                trigger: "axis", // 
                formatter: function (params, ticket, callback) {
                    let total = 0; // 
                    for (let i = 0; i < params.length; i++) {
                        total += params[i].data;
                    }
                    let obj = params.map((item, index) => {
                        let percent = total
                            ? ((item.value / total) * 100).toFixed(1)
                            : "0.0%";

                        // 
                        let dotColor =
                            '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:' +
                            item.color +
                            '"></span>';
                        return (
                            dotColor +
                            item.seriesName +
                            ": " +
                            item.value +
                            "(" +
                            percent +
                            "%" +
                            ")" +
                            "</br>"
                        );
                    });
                    return obj.join(""); // ','
                },

                axisPointer: {
                    // // 
                    type: "line", // 'line' | 'shadow'
                },
            },
            legend: {
                top: "2%", // ,
                data: this.legendData,
            },
            grid: {
                left: "0.3%", //grid 
                right: "2%", // grid 
                bottom: "1%", // grid 
                containLabel: true, // grid 
            },
            //  grid  x 
            xAxis: {
                type: this.axisXType, // x 'category'  data 
                data: this.barData.xAxisData,
                axisLabel: {
                    interval: 0, // 
                    rotate: 30, // 
                    // formatter: function(value) {
                    //     // 
                    //     return value.split("").join("\n");
                    // }
                },
            },
            //  grid  y 
            yAxis: [
                {
                    type: this.axisYType, // y 
                    data: this.barData.yAxisData,
                    axisLabel: {
                        interval: 0, // 
                        rotate: 30, // 
                        // formatter: function(value) {
                        //     // 
                        //     return value.split("").join("\n");
                        // }
                    },
                },
            ],
            series: this.barData.seriesData,
        };

        if (this.color.length) {
            option["color"] = this.color;
        }

        let chartDom = document.getElementById(this.elementID);
        const chartObj = this.$echarts.init(chartDom);

        if (this.axisXType == "value") {
            option["xAxis"]["max"] = function (value) {
                if (value) {
                    if (value.max > 1000) {
                        return value.max + 150; // 
                    } else if (value.max > 500) {
                        return value.max + 100;
                    } else if (value.max > 100) {
                        return value.max + 50;
                    } else if (value.max > 50) {
                        return value.max + 20;
                    } else {
                        return value.max + 10;
                    }
                } else {
                    return 0;
                }
            };
        let barHeight = this.barData.seriesData.length * 30 + 100; // 
        chartDom.style.height = barHeight + "px";

        }

        chartObj.setOption(option);
        chartObj.resize();

        window.addEventListener("resize", function () {
            chartObj.resize();
        });
    },
};
</script>



