<template>
    <div>
        <!--  -->
        <el-table
            :data="
                defectsCreatedIndividual.slice(
                    (currentPage - 1) * pageSize,
                    currentPage * pageSize
                )
            "
            border
            stripe
            highlight-current-row
        >
            <el-table-column
                prop="creater"
                label=""
                min-width="16%"
                show-overflow-tooltip
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="DI"
                label=""
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="totalCreated"
                label=""
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="deadly"
                label=""
                show-overflow-tooltip
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="critical"
                label=""
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="general"
                label=""
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                label=""
                prop="minor"
                min-width="14%"
                header-align="center"
                align="center"
            >
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="totalRows"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
        ></el-pagination>
    </div>
</template>

<script>
export default {
    props: ["defectsCreatedIndividual"],
    data() {
        return {
            totalRows: this.defectsCreatedIndividual.length,
            pageSizes: [10, 20, 30, 40, 50, 80, 100, 150], //
            pageSize: 10, // 
            currentPage: 1, // 
        };
    },

    methods: {
        // 
        handleSizeChange(pageSize) {
            this.currentPage = 1;
            this.pageSize = pageSize;
        },
        // 
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage;
        },
    },
};
</script>


