<template>
    <div>
        <!--  -->
        <el-table
            :data="caseExecutionIndividual.slice((currentPage-1)*pageSize,currentPage*pageSize)"
            border
            stripe
            highlight-current-row
        >
            <el-table-column
                prop="username"
                label=""
                min-width="16%"
                show-overflow-tooltip
                header-align="center"
                align="center"
            ></el-table-column>
         
            <el-table-column
                prop="testcaseAssigned"
                label=""
                show-overflow-tooltip
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="testcaseExecuted"
                label=""
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="testcasePassed"
                label=""
                min-width="14%"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                label=""
                prop="testcaseExecutionRate"
                min-width="14%"
                header-align="center"
                align="center"
            >
                <template slot-scope="scope">{{scope.row.testcaseExecutionRate}}%</template>
            </el-table-column>
            <el-table-column
                label=""
                prop="testcasePassRate"
                min-width="14%"
                show-overflow-tooltip
                header-align="center"
                align="center"
            >
                <template slot-scope="scope">{{scope.row.testcasePassRate}}%</template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination           
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="totalRows"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
        ></el-pagination>
    </div>
</template>

<script>
export default {
    props: ["caseExecutionIndividual"],
    data() {
        return {
            totalRows: this.caseExecutionIndividual.length,
            pageSizes: [10, 20, 30, 40, 50, 80, 100, 150], //
            pageSize: 10, // 
            currentPage: 1 // 
        };
    },

    methods: {
        // 
        handleSizeChange(pageSize) {
            this.currentPage = 1;
            this.pageSize = pageSize;
        },
        // 
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage;
        }
    }
};
</script>


