<template>
    <el-dialog
        title=""
        width="45%"
        :visible="dialogVisible"
        :show-close="showClose"
        :close-on-click-modal="closeOnClickModal"
        :close-on-press-escape="closeOnPressEscape"
        @close="onCloseDialog"
    >
        <div class="my-dialog-body">
            <el-form
                :model="dialogForm"
                :rules="rules"
                ref="dialogForm"
                label-width="130px"
            >
                <el-form-item label="" prop="platform">
                    <el-select
                        v-model="dialogForm.platform"
                        :multiple="false"
                        filterable
                        placeholder=""
                        style="width: 100%"
                        @change="onPlatformChange"
                    >
                        <el-option
                            v-for="item in platformOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="project">
                    <el-select
                        v-model="dialogForm.project"
                        :multiple="false"
                        value-key="id"
                        clearable
                        filterable
                        placeholder=""
                        style="width: 100%"
                        @clear="clearProject"
                    >
                        <el-option
                            v-for="item in projectOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item"
                        ></el-option>
                    </el-select>
                    <br />
                    <span
                        ></span
                    >
                </el-form-item>
                <el-form-item
                    v-if="dialogForm.platform == 'jira'"
                    label=""
                    prop="defectIssueTypeId"
                >
                    <el-select
                        v-model="dialogForm.defectIssueTypeId"
                        filterable
                        placeholder=""
                        style="width: 100%"
                    >
                        <el-option
                            v-for="item in issueTypeOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item
                    label=""
                    v-if="dialogForm.platform == 'jira'"
                >
                    <div>
                        <el-select
                            v-model="systemField"
                            :multiple="false"
                            filterable
                            placeholder=""
                            style="width: 45%"
                        >
                            <el-option
                                v-for="item in systomFieldsOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                        <el-select
                            v-model="platformField"
                            :multiple="false"
                            filterable
                            placeholder=""
                            style="width: 45%"
                        >
                            <el-option
                                v-for="item in customFieldOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                        <span class="my-icon-wrapper">
                            <i
                                class="el-icon-plus"
                                @click="addCustomFieldMap"
                            ></i>
                        </span>
                    </div>
                </el-form-item>
                <el-form-item
                    v-for="(item, index) in dialogForm.customFieldMapArray"
                    :key="item.key"
                >
                    <el-select
                        v-model="item.key"
                        :multiple="false"
                        filterable
                        placeholder=""
                        style="width: 45%"
                        @change="onSystemFieldSelectChange($event, index)"
                        @visible-change="
                            onSystemFieldVisibleChange($event, item.key)
                        "
                    >
                        <el-option
                            v-for="item in systomFieldsOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>
                    <el-select
                        v-model="item.value"
                        :multiple="false"
                        filterable
                        placeholder=""
                        style="width: 45%"
                    >
                        <el-option
                            v-for="item in customFieldOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>

                    <span class="my-icon-wrapper">
                        <i
                            class="el-icon-delete"
                            @click="delCustomFieldMap($event, index, item.key)"
                        ></i>
                    </span>
                </el-form-item>
                <el-form-item label="" prop="defectSeverityMap">
                    <el-tooltip
                        effect="dark"
                        content=""
                        placement="right-start"
                    >
                        <i class="fa fa-question-circle" aria-hidden="true"></i>
                    </el-tooltip>
                    <el-input
                        :placeholder="severityMapPlaceholder"
                        v-model="dialogForm.defectSeverityMap"
                        type="textarea"
                        :autosize="{ minRows: 2 }"
                        @blur="onSeverityMapBlur"
                    ></el-input>
                </el-form-item>
                <el-form-item label="" prop="defectStatusMap">
                    <el-tooltip
                        effect="dark"
                        content=""
                        placement="right-start"
                    >
                        <i class="fa fa-question-circle" aria-hidden="true"></i>
                    </el-tooltip>
                    <el-input
                        :placeholder="statusMapPlaceholder"
                        v-model="dialogForm.defectStatusMap"
                        type="textarea"
                        :autosize="{ minRows: 2 }"
                        @blur="onStatusMapBlur"
                    ></el-input>
                </el-form-item>

                <el-form-item class="dialog-footer-form-item">
                    <div class="my-dialog-footer">
                        <el-button @click="closeDialog"></el-button>
                        <el-button type="primary" @click="save('dialogForm')"
                            ></el-button
                        >
                    </div>
                </el-form-item>
            </el-form>
        </div>
    </el-dialog>
</template>

<script>
import { elDialogMixin } from "@/common/mixins/elDialogMixin";

export default {
    props: ["dialogVisible", "dialogFormData", "row"],
    mixins: [elDialogMixin],
    data() {
        return {
            dialogForm: JSON.parse(JSON.stringify(this.dialogFormData)),
            // 
            platformOptions: [
                { id: "jira", name: "jira" },
                { id: "zentao", name: "" },
                // { id: "tapd", name: "tapd" },
            ],
            projectOptions: [], // 
            issueTypeOptions: [], // 
            defectCustomFieldsOptions: [],
            customFieldOptions: [], // 
            rules: {
                platform: [
                    {
                        type: "string",
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ],
            },
            
            severityMapPlaceholder:
                'JSON,{"Critical":"", "Major":""}',
            statusMapPlaceholder:
                'JSON,{"":"","":"":"":"","reopen":""}',
            severityMapFormatError: false, // 
            statusMapFormatError: false, // 
            systemField: "", // 
            systemFieldOld: "", // 
            platformField: "", // 
        };
    },
    methods: {
        onPlatformChange(value) {
            if (!value) {
                return;
            }
            this.dialogForm.project = null;
            this.platformField = "";
            this.dialogForm.customFieldMapArray = [];

            this.getProjectOptions();
            if (this.dialogForm.platform == "jira") {
                this.getJiraIssueTypeOptions();
                this.getJiraIssueCustomFieldOptions();
            } else if (this.dialogForm.platform == "zentao") {
                this.getZentaoDefectCustomFieldOptions();
            } else {
            }
        },
        // 
        getProjectOptions() {
            this.projectOptions = [];

            this.$api.project
                .getPlatformProjects({
                    platform: this.dialogForm.platform,
                })
                .then((res) => {
                    if (res.success) {
                        this.projectOptions = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // 
        getSystomFieldsOptions() {
            this.systomFieldsOptions = [];

            this.$api.project
                .getSystomFieldsOptions()
                .then((res) => {
                    if (res.success) {
                        this.systomFieldsOptions = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // 
        addCustomFieldMap() {
            if (!this.systemField) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (!this.platformField) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (
                this.dialogForm.customFieldMap.hasOwnProperty(this.systemField)
            ) {
                this.$message.warning("");
                return;
            }
            this.dialogForm.customFieldMap[
                this.systemField
            ] = this.platformField;
            this.dialogForm.customFieldMapArray.splice(0, 0, {
                key: this.systemField,
                value: this.platformField,
            });
        },
        // 
        delCustomFieldMap(event, index, key) {
            delete this.dialogForm.customFieldMap[key];
            this.dialogForm.customFieldMapArray.splice(index, 1);
        },
        // 
        onSystemFieldSelectChange(value, index) {
            if (this.dialogForm.customFieldMap.hasOwnProperty(value)) {
                this.$message.warning("");
                this.dialogForm.customFieldMapArray[
                    index
                ].key = this.systemFieldOld;

                return;
            }
        },
        // 
        onSystemFieldVisibleChange(value, systemField) {
            if (value) {
                this.systemFieldOld = systemField;
            }
        },
        // jira
        getJiraIssueTypeOptions() {
            this.issueTypeOptions = [];
            this.$api.project
                .getJiraIssueTypes()
                .then((res) => {
                    if (res.success) {
                        this.issueTypeOptions = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // jira
        getJiraIssueCustomFieldOptions() {
            this.customFieldOptions = [];
            this.$api.project
                .getJiraIssueCustomFields()
                .then((res) => {
                    if (res.success) {
                        this.customFieldOptions = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // 
        getZentaoDefectCustomFieldOptions() {
            this.customFieldOptions = [];
            this.$api.project
                .getZentaoDefectCustomFields()
                .then((res) => {
                    if (res.success) {
                        this.customFieldOptions = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // 
        bindProject() {
            if (this.severityMapFormatError) {
                this.$alert(
                    "json",
                    "",
                    {
                        confirmButtonText: "",
                    }
                );
                return;
            }
            if (this.statusMapFormatError) {
                this.$alert(
                    "json",
                    "",
                    {
                        confirmButtonText: "",
                    }
                );

                return;
            }

            this.dialogForm.customFieldMap = {};
            for (
                let i = 0;
                i < this.dialogForm.customFieldMapArray.length;
                i++
            ) {
                this.dialogForm.customFieldMap[
                    this.dialogForm.customFieldMapArray[i].key
                ] = this.dialogForm.customFieldMapArray[i].value;
            }

            let data = {
                projectId: this.row.id,
                platform: this.dialogForm.platform,
                platformProjectId: this.dialogForm.project.id,
                platformProjectName: this.dialogForm.project.name,
                defectIssueTypeId: this.dialogForm.defectIssueTypeId,
                defectStatusMap: this.dialogForm.defectStatusMap,
                defectSeverityMap: this.dialogForm.defectSeverityMap,
                customFieldMap: this.dialogForm.customFieldMap,
            };

            this.$api.project
                .bindPlatformProject(data)
                .then((res) => {
                    if (res.success) {
                        this.$message.success(res.msg);
                        for (let key in res.data) {
                            if (key in this.row) {
                                this.row[key] = res.data[key];
                            }
                        }
                        this.closeDialog();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        // 
        unbindProject() {
            let data = { projectId: this.row.id };
            this.$api.project
                .unbindPlatformProject(data)
                .then((res) => {
                    if (res.success) {
                        this.$message.success(res.msg);
                        for (let key in res.data) {
                            if (key in this.row) {
                                // key
                                this.row[key] = res.data[key];
                            }
                        }
                        this.closeDialog();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        clearProject() {
            this.dialogForm.project = null;
        },
        onSeverityMapBlur() {
            try {
                let temp = this.dialogForm.defectSeverityMap;
                temp = temp.trim();

                if (temp) {
                    if (temp.startsWith("{")) {
                        JSON.parse(temp);
                    } else {
                        this.severityMapFormatError = true;
                        this.$message.error(
                            "json"
                        );
                        return;
                    }
                }
                this.severityMapFormatError = false;
            } catch (err) {
                this.severityMapFormatError = true;
                this.$message.error(
                    "json:" +
                        err.message
                );
            }
        },
        onStatusMapBlur() {
            try {
                let temp = this.dialogForm.defectStatusMap;
                temp = temp.trim();
                if (temp) {
                    if (temp.startsWith("{")) {
                        JSON.parse(temp);
                    } else {
                        this.statusMapFormatError = true;
                        this.$message.error(
                            "json"
                        );
                        return;
                    }
                }
                this.statusMapFormatError = false;
            } catch (err) {
                this.statusMapFormatError = true;
                this.$message.error(
                    "json:" + err.message
                );
            }
        },
        // 
        save(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (this.dialogForm.project) {
                        this.bindProject();
                    } else {
                        this.unbindProject();
                    }
                } else {
                    // 
                }
            });
        },
    },
    created() {
        this.getProjectOptions();
        this.getSystomFieldsOptions();
        if (this.dialogForm.platform == "jira") {
            this.getJiraIssueTypeOptions();
            this.getJiraIssueCustomFieldOptions();
        } else if (this.dialogForm.platform == "zentao") {
            this.getZentaoDefectCustomFieldOptions();
        }
    },
};
</script>

<style scoped>
.my-dialog-footer {
    text-align: center;
    margin-left: -130px;
}

.my-dialog-body .el-form-item {
    margin-right: 20px;
}

.my-icon-wrapper {
    display: inline-block;
    position: relative;
    top: 3px;
    margin-left: 5px;
    font-size: 20px;
    line-height: 37px;
    height: 37px;
}

.el-tooltip {
    position: relative;
    float: left;
    left: -15px;
}

/deep/ .el-form-item {
    margin-bottom: 20px;
}
</style>



