<template>
    <div class="view-wrapper" ref="viewWrapper" v-resize="setTableBodySize">
        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryForm" size="small">
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.account"
                        placeholder=""
                        clearable
                        style="width:230px"
                    ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input
                        v-model="queryForm.name"
                        placeholder=""
                        clearable
                        style="width:230px"
                    ></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-select
                        v-model="queryForm.isActive"
                        clearable
                        @clear="clearStatus"
                        style="width: 100px;"
                    >
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="queryRows('clickQuery')"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button :size="buttonSize" @click="addUser"></el-button>
                <el-button :size="buttonSize" @click="deleteRows"></el-button>
            </el-button-group>
        </div>
        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            :empty-text="emptyText"
            border
            stripe
            class="my-table"
            @selection-change="onSelectChange"
            highlight-current-row
        >
            <!---->
            <el-table-column type="selection" :width="checkBoxColWidth" align="center"></el-table-column>
            <!-- <el-table-column prop="id" label="ID" width="70px" header-align="center" align="center"></el-table-column> -->
            <el-table-column
                prop="account"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
                min-width="120px"
            ></el-table-column>
            <el-table-column
                prop="name"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="center"
                width="95px"
            ></el-table-column>
            <el-table-column
                prop="email"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
                min-width="230px"
            ></el-table-column>
            <el-table-column
                prop="mobile"
                label=""
                header-align="center"
                align="center"
                width="115px"
            ></el-table-column>
            <el-table-column
                prop="jobNumber"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
                width="110px"
            ></el-table-column>
            <el-table-column
                prop="roles"
                label=""
                header-align="center"
                min-width="210px"
                align="left"
            >
                <template slot-scope="scope">
                    <span v-for="role in scope.row.roles" :key="role.id" @click.stop>
                        {{ role.name }}
                        <i
                            class="el-icon-close"
                            aria-hidden="true"
                            @click.prevent="deleteRelatedRole(scope.row, role)"
                        >;&nbsp;&nbsp;</i>
                    </span>
                    <el-button
                        size="mini"
                        @click="bindRole(scope.$index, scope.row)"
                        class="row-operation-btn"
                    >
                        <i class="fa fa-plus" aria-hidden="true"></i>
                    </el-button>
                </template>
            </el-table-column>
            <el-table-column
                prop="groups"
                label=""
                header-align="center"
                min-width="210px"
                align="left"
            >
                <template slot-scope="scope">
                    <span v-for="group in scope.row.groups" :key="group.id" @click.stop>
                        {{ group.name }}
                        <i
                            class="el-icon-close"
                            aria-hidden="true"
                            @click.prevent="deleteRelatedGroup(scope.row, group)"
                        >;&nbsp;&nbsp;</i>
                    </span>
                    <el-button
                        size="mini"
                        @click="bindGroup(scope.$index, scope.row)"
                        class="row-operation-btn"
                    >
                        <i class="fa fa-plus" aria-hidden="true"></i>
                    </el-button>
                </template>
            </el-table-column>

            <el-table-column
                prop="isActive"
                label=""
                width="80px"
                header-align="center"
                align="center"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="scope.row.isActive"
                        size="mini"
                        @click="toggleUserStatus(scope.$index, scope.row)"
                    ></el-button>
                    <el-button
                        v-else
                        size="mini"
                        @click="toggleUserStatus(scope.$index, scope.row)"
                    ></el-button>
                </template>
            </el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="lastLoginTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column label="" align="left" width="250px" class="operation-col">
                <template slot-scope="scope">
                    <el-button size="mini" @click="editUser(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" @click="bindAccount(scope.$index, scope.row)"></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="resetPasswd(scope.$index, scope.row)"
                    ></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteOneRow(scope.$index, scope.row)"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!-- \ -->
        <sys-user-dialog
            v-if="userDialogVisible"
            :dialogVisible.sync="userDialogVisible"
            :dialogTitle="userDialogTitle"
            :tableData="tableData"
            :dialogFormData="userData"
            :operateOneSelf="operateOneSelf"
            :row="row"
            :statusOptions="statusOptions"
        ></sys-user-dialog>

        <!--  -->
        <bind-role-dialog
            v-if="bindRoleDialogVisible"
            :dialogVisible.sync="bindRoleDialogVisible"
            :row="row"
        ></bind-role-dialog>

        <!--  -->
        <bind-group-dialog
            v-if="bindGroupDialogVisible"
            :dialogVisible.sync="bindGroupDialogVisible"
            :row="row"
        ></bind-group-dialog>

        <!--  -->
        <related-account-dlg
            v-if="relatedAccountDlgVisible"
            :dialogVisible.sync="relatedAccountDlgVisible"
            :userId="row.id"
        ></related-account-dlg>
    </div>
</template>

<script>
import { elTableMixin } from "@/common/mixins/elTableMixin";
import SysUserDialog from "./SysUser/SysUserDialog";
import BindRoleDialog from "./SysUser/BindRoleDialog";
import BindGroupDialog from "./SysUser/BindGroupDialog";
import RelatedAccountDlg from "./SysUser/RelatedAccountDialog";

import { mapState } from "vuex";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

export default {
    mixins: [elTableMixin, elButtonMixin],

    data() {
        return {
            // 
            queryForm: {
                account: "", // 
                name: "", // 
                isActive: true, // 
            },
            userData: null, // 
            operateOneSelf: false, // 
            userDialogVisible: false, // \true -  false - 
            userDialogTitle: "", // 

            bindRoleDialogVisible: false, // 
            bindGroupDialogVisible: false, // 
            relatedAccountDlgVisible: false, // 
            statusOptions: [
                { label: "", value: true },
                { label: "", value: false },
            ],
        };
    },
    components: {
        SysUserDialog,
        BindRoleDialog,
        BindGroupDialog,
        RelatedAccountDlg,
    },
    methods: {
        addUser() {
            if (!this.userDialogVisible) {
                this.userData = {
                    account: "", //
                    name: "", // 
                    email: "", // 
                    mobile: "", // 
                    jobNumber:"", // 
                    isActive: true, // 
                };

                this.operateOneSelf = false;

                this.userDialogVisible = true;
                this.userDialogTitle = "";
            }
        },
        editUser(index, row) {
            if (!this.userDialogVisible) {
                this.userData = {
                    id: row.id,
                    account: row.account,
                    name: row.name,
                    email: row.email,
                    mobile: row.mobile,
                    jobNumber:row.jobNumber, 
                    isActive: row.isActive,
                };

                if (this.userId == row.id) {
                    this.operateOneSelf = true; // 
                } else {
                    this.operateOneSelf = false;
                }

                this.row = row;
                this.userDialogVisible = true;
                this.userDialogTitle = "";
            }
        },
        // 
        deleteOneRow(index, row) {
            if (row.isSuperuser) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (this.userId == row.id) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:delete"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            this.deleteRow(index, row, this.$api.sysUser.deleteSysUser);
        },

        // 
        deleteRows() {
            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:delete"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    if (!this.rowsSelected.length) {
                        this.$alert("", "", {
                            confirmButtonText: "",
                        });
                        return;
                    }
                    let rowIds = []; // ID
                    for (let i = 0; i < this.rowsSelected.length; i++) {
                        if (this.rowsSelected[i].isSuperuser) {
                            this.$message.info(
                                "" +
                                    this.rowsSelected[i].account +
                                    ""
                            );
                            continue;
                        }

                        if (this.rowsSelected[i].id == this.userId) {
                            this.$message.info(
                                this.rowsSelected[i].account +
                                    ""
                            );
                            continue;
                        }
                        rowIds.push(this.rowsSelected[i].id);
                    }

                    if (!rowIds.length) {
                        this.$message({
                            message: "",
                            type: "success",
                        });
                        return;
                    }

                    // 
                    this.$api.sysUser
                        .deleteSysUsers({
                            rowIds: rowIds,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message.success(res.msg);
                                // 
                                this.total =
                                    this.total - this.rowsSelected.length;

                                for (
                                    let i = 0;
                                    i < this.rowsSelected.length;
                                    i++
                                ) {
                                    this.tableData.splice(
                                        this.tableData.indexOf(
                                            this.rowsSelected[i]
                                        ),
                                        1
                                    );
                                }

                                if (!this.tableData.length && this.total != 0) {
                                    // 

                                    let totalPages = Math.ceil(
                                        this.total / this.pageSize
                                    );

                                    if (this.currentPage > totalPages) {
                                        // 
                                        this.currentPage -= 1;
                                    } else {
                                        // 
                                    }
                                    this.queryRows();
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        // \
        toggleUserStatus(index, row) {
            this.row = row;

            if (this.userId == row.id && row.isActive) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:toggleStatus"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            let targetStatus = row.isActive ? "" : "";
            this.$confirm("" + targetStatus + "?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.sysUser
                        .toggleUserStatus({
                            id: row.id,
                            isActive: !row.isActive,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                for (let key in res.data) {
                                    if (key in row) {
                                        // key
                                        row[key] = res.data[key];
                                    }
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        bindRole(index, row) {
            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:bindRole"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            if (!this.bindRoleDialogVisible) {
                this.bindRoleDialogVisible = true;
                this.row = row;
            }
        },
        deleteRelatedRole(row, role) {
            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:deleteRole"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.sysUser
                        .deleteRelatedRole({
                            userId: row.id,
                            roleId: role.id,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                const index = row.roles.indexOf(role);
                                if (index != -1) {
                                    row.roles.splice(index, 1);
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        bindGroup(index, row) {
            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:bindGrp"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }
            if (!this.bindGroupDialogVisible) {
                this.bindGroupDialogVisible = true;
                this.row = row;
            }
        },
        deleteRelatedGroup(row, group) {
            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:deleteGrp"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.sysUser
                        .deleteRelatedGroup({
                            userId: row.id,
                            groupId: group.id,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                const index = row.groups.indexOf(group);
                                if (index != -1) {
                                    row.groups.splice(index, 1);
                                }
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        bindAccount(index, row) {
            if (!this.isSuperuser) {
                if (this.userId != row.id) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }
            if (!this.relatedAccountDlgVisible) {
                this.relatedAccountDlgVisible = true;
                this.row = row;
            }
        },
        // isActive=""
        clearStatus() {
            this.queryForm.isActive = "";
        },
        resetPasswd(index, row) {
            if (!this.isSuperuser) {
                if (
                    !this.$customUtils.permission.hasPermission(
                        "sys:user:resetPasswd"
                    )
                ) {
                    this.$alert("", "", {
                        confirmButtonText: "",
                    });
                    return;
                }
            }

            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.$api.sysUser
                        .resetPasswd({
                            userId: row.id,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message.success(res.msg);
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
        getUserPersonalInfo() {
            new Promise(this.$customUtils.userInfo.getCurrentUserInfo)
                .then((res) => {
                    this.$store.commit("setUserId", res.data.id);
                    this.$store.commit("setIsSuperUser", res.data.isSuperuser); // 
                })
                .catch((err) => {
                    this.$message.error(res.msg || res.message);
                });
        },
    },
    created() {
        if (this.isSuperuser == undefined) {
            this.getUserPersonalInfo();
        }
    },
    updated() {
        this.setTableBodySize();
    },
    created() {
        this.queryRowsAPI = this.$api.sysUser.getSysUsers;
        this.queryRows();
    },
    computed: {
        ...mapState({
            isSuperuser: (state) => state.user.isSuperuser,
            userId: (state) => state.user.userId,
        }),
    },
};
</script>

<style lang="scss" scoped>
.el-button {
    margin-left: 1px;
    margin-right: 0px;
    padding-left: 5px;
    padding-right: 5px;
}

/deep/ .cell {
    padding-left: 10px;
    padding-right: 10px;
}
</style>



