<template>
    <div
        class="view-wrapper"
        ref="viewWrapper"
        v-resize="setTableBodySize"
        v-loading="loading"
        :element-loading-text="elementLoadingText"
    >
        <!--  -->
        <div class="table-query-form" ref="queryForm">
            <el-form :inline="true" :model="queryForm" size="small">
                <el-form-item label="">
                    <el-select
                        v-model="queryForm.executionPhase"
                        clearable
                        style="width: 115px"
                    >
                        <el-option
                            v-for="item in executionPhaseOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select
                        v-model="queryForm.executedEachSprint"
                        clearable
                        style="width: 85px"
                    >
                        <el-option label="" value="Y"></el-option>
                        <el-option label="" value="N"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item
                    label=""
                    v-if="currentModule == 'bindPlanAndCaseDialog'"
                >
                    <el-select
                        v-model="queryForm.showHistoryOnly"
                        clearable
                        style="width: 85px"
                    >
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>

                <span v-if="!isQueryItemsCollapsed">
                    <el-form-item label="">
                        <el-date-picker
                            style="width: 330px"
                            v-model="createTime"
                            type="datetimerange"
                            placeholder=""
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            size="mini"
                            :default-time="['00:00:00', '23:59:59']"
                            value-format="yyyy-MM-dd HH:mm:ss"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="queryForm.priority"
                            :multiple="true"
                            clearable
                            style="width: 170px"
                        >
                            <el-option
                                v-for="item in priorityOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="queryForm.executionMethod"
                            clearable
                            @clear="clearExecutionMethod"
                            style="width: 85px"
                        >
                            <el-option
                                label=""
                                value="handwork"
                            ></el-option>
                            <el-option
                                label=""
                                value="automation"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="queryForm.recursive"
                            style="width: 60px"
                        >
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.name"
                            placeholder=""
                            :style="getInputWidthStyle(queryForm.name, 125)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.creater"
                            placeholder=""
                            :style="getInputWidthStyle(queryForm.creater, 85)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.updater"
                            placeholder=""
                            :style="getInputWidthStyle(queryForm.creater, 85)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            v-model="queryForm.tag"
                            placeholder=""
                            :style="getInputWidthStyle(queryForm.creater, 110)"
                        ></el-input>
                    </el-form-item>
                </span>
                <el-form-item>
                    <el-button
                        type="primary"
                        @click="queryRows('clickQuery')"
                        :disabled="showCaseDialog"
                        ></el-button
                    >
                    <el-button @click="restQueryRowsForm"></el-button>
                </el-form-item>
                <span
                    @click.prevent="toggleQueryItems"
                    style="
                        height: 32px;
                        line-height: 32px;
                        font-size: 14px;
                        color: #606266;
                    "
                >
                    <i
                        class="el-icon-arrow-up el-icon--right"
                        v-show="!isQueryItemsCollapsed"
                        ></i
                    >
                    <i
                        class="el-icon-arrow-down el-icon--right"
                        v-show="isQueryItemsCollapsed"
                        ></i
                    >
                </span>
            </el-form>
        </div>
        <!--  -->
        <div ref="tableToolbar" class="table-toolbar">
            <el-button-group>
                <el-button
                    :size="buttonSize"
                    @click="newCase"
                    :disabled="iscurrSuiteRoot || showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="batchDeleteCases"
                    :disabled="showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="copyTestCasesToSprint"
                    :disabled="showCaseDialog || suiteType == 'base'"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="copyCases"
                    :disabled="showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="cutCases"
                    :disabled="showCaseDialog"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    :disabled="showCaseDialog"
                    @click="importTestCases"
                    ></el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="exportTestCases"
                    :disabled="showCaseDialog"
                    >(Excel)</el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="exportXmindTestCases"
                    :disabled="showCaseDialog"
                    >(XMind)</el-button
                >
                <el-button
                    :size="buttonSize"
                    @click="batchUpdateTestCases"
                    :disabled="showCaseDialog"
                    ></el-button
                >
                <el-dropdown>
                    <el-button :size="buttonSize" :disabled="showCaseDialog">
                        
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-button-group>
        </div>
        <!--  -->
        <el-table
            ref="myTable"
            :data="tableData"
            border
            stripe
            :empty-text="emptyText"
            class="my-table sprint-testcase-table"
            :lazy="false"
            row-key="guid"
            :span-method="mergeColumns"
            :cell-class-name="getCellClassName"
            :row-class-name="getRowClassName"
            @row-click="clickRow"
            @selection-change="onSelectChange"
            @select="onSelectRow"
            @select-all="onSelectAllRow"
            @sort-change="sortChange"
            highlight-current-row
        >
            <!---->
            <el-table-column
                type="selection"
                :width="checkBoxColWidth"
                align="center"
            ></el-table-column>
            <!-- <el-table-column
                prop="customNo"
                label=""
                width="150px"
                header-align="center"
                align="left"
            ></el-table-column>-->
            <el-table-column
                prop="name"
                label=""
                show-overflow-tooltip
                header-align="center"
                align="left"
                sortable="custom"
                min-width="250px"
            ></el-table-column>
            <el-table-column
                prop="priority"
                label=""
                width="90px"
                header-align="center"
                align="center"
                sortable="custom"
            ></el-table-column>
            <!-- <el-table-column
                prop="executionMethod"
                label=""
                width="80px"
                header-align="center"
                align="center"
            ></el-table-column>-->
            <el-table-column
                prop="createrName"
                label=""
                width="90px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="updaterName"
                label=""
                width="90px"
                header-align="center"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="createTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="updateTime"
                label=""
                width="160px"
                header-align="center"
                align="center"
                sortable="custom"
            ></el-table-column>
            <el-table-column label="" align="left" width="205px">
                <template slot-scope="scope">
                    <!-- <el-button
                        size="mini"
                        @click="editCase(scope.$index, scope.row)"
                    ></el-button>-->
                    <el-button
                        size="mini"
                        @click="viewCase(scope.$index, scope.row)"
                        ></el-button
                    >
                    <el-button
                        size="mini"
                        @click="copyCase(scope.$index, scope.row)"
                        ></el-button
                    >
                    <el-button
                        size="mini"
                        type="danger"
                        @click="deleteOneCase(scope.$index, scope.row)"
                        ></el-button
                    >
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            ref="tablePagination"
            class="table-pagination"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="total, sizes, prev, pager, next"
            :hide-on-single-page="false"
            :total="total"
            prev-text=""
            next-text=""
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :pager-count="pagerCount"
        ></el-pagination>

        <!---->
        <transition name="slide-fade">
            <sprint-case-dialog
                v-if="caseDialogLoaded"
                :caseDialogLoaded.sync="caseDialogLoaded"
                :caseDialogEditing.sync="caseDialogEditing"
                :caseDialogTitle.sync="caseDialogTitle"
                :caseDialogVisible.sync="showCaseDialog"
                :tableData.sync="tableData"
                :caseData="caseData"
                :row.sync="row"
                :currentPage.sync="currentPage"
                :pageSize="pageSize"
                :total.sync="total"
                :operation.sync="operation"
                :caseOrder.sync="caseOrder"
                :currentModule="currentModule"
                :priorityOptions="priorityOptions"
                :executionPhaseOptions="executionPhaseOptions"
                :executedEachSprintDict="executedEachSprintDict"
            ></sprint-case-dialog>
        </transition>

        <!--  -->
        <import-case-dialog
            v-if="importCaseDialogVisible"
            :dialogVisible.sync="importCaseDialogVisible"
            :dataForImportCase="dataForImportCase"
        ></import-case-dialog>

        <!--  -->
        <copy-case-to-sprint-dlg
            v-if="copyCaseToSprintDlgVisible"
            :dialogVisible.sync="copyCaseToSprintDlgVisible"
            :currentModule="currentModule"
        ></copy-case-to-sprint-dlg>

        <!---->
        <batch-update-cases-dlg
            v-if="updateCasesDlgVisible"
            :dialogVisible.sync="updateCasesDlgVisible"
            :casesInfoForUpdate="casesInfoForUpdate"
            :suiteId="suiteID"
            :executionPhaseOptions="executionPhaseOptions"
            :executedEachSprintDict="executedEachSprintDict"
        ></batch-update-cases-dlg>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { elTableMixin } from "@/common/mixins/elTableMixin";
import { elButtonMixin } from "@/common/mixins/elButtonMixin";

import SprintCaseDialog from "./SprintCaseDialog";
import ImportCaseDialog from "./ImportCaseDialog";
import CopyCaseToSprintDlg from "./CopyCaseToSprintDlg";
import BatchUpdateCasesDlg from "./BatchUpdateCasesDlg";

export default {
    mixins: [elTableMixin, elButtonMixin],
    props: [
        "currentModule",
        "relatedCaseGuidList",
        "testplanId",
        "productID",
        "sprintID",
    ],
    data() {
        return {
            // 
            queryForm: {
                startTime: "", // 
                endTime: "", // 
                name: "", // 
                priority: [], // 
                executionPhase: "", // 
                executionMethod: "handwork", // 
                executedEachSprint: "", // 
                showHistoryOnly: "", // 
                recursive: "1", // 
                creater: "", // 
                updater: "", // 
                tag: "", // 
                sort: "-id", // 
            },
            caseData: null, // 
            pageSizes: [10, 15, 20, 25, 30, 40, 50, 100, 150], //
            pageSize: 15, // 
            showCaseDialog: false, // / true false 
            caseDialogLoaded: false, //  true  false 
            caseDialogEditing: false, //  true  false 
            caseDialogTitle: "", // 
            operation: "viewCase", //   viewCase  newCase  editCase  copyCase  deleteCase
            guidsOfRelatedOldCases: [], // ID
            caseOrder: 1, // ,, 1
            importCaseDialogVisible: false, //  true -  false -
            copyCaseToSprintDlgVisible: false, //  true -  false -
            dataForImportCase: {}, // 
            casesCopyToSprint: [], // 
            priorityOptions: [
                { label: "P1", value: "P1" },
                { label: "P2", value: "P2" },
                { label: "P3", value: "P3" },
                { label: "P4", value: "P4" },
            ],
            executionPhaseOptions: [], // 
            executedEachSprintDict: {}, // 
            loading: false, // 
            elementLoadingText: "",
            updateCasesDlgVisible: false, // 
            casesInfoForUpdate: [], // 
            confirmRadioValue: "1", // confirm radiovalue
            isQueryItemsCollapsed: true,
        };
    },
    components: {
        SprintCaseDialog,
        ImportCaseDialog,
        CopyCaseToSprintDlg,
        BatchUpdateCasesDlg,
    },
    methods: {
        // 
        restQueryRowsForm() {
            // this.$refs["caseTableQueryForm"].resetFields(); // 
            this.queryForm.startTime = "";
            this.queryForm.endTime = "";
            this.queryForm.name = "";
            this.queryForm.priority = [];
            this.queryForm.executionPhase = "";
            this.queryForm.executionMethod = "handwork";
            this.queryForm.executedEachSprint = "";
            this.queryForm.recursive = "1";
            this.queryForm.creater = "";
            this.queryForm.updater = "";
            this.queryForm.tag = "";
        },
        // 
        queryRows(action, resolve, reject) {
            if (this.suiteID == -1 || this.suiteID == -2) {
                // suiteIDstate-1-2
                this.total = 0;
                this.tableData = [];
                return;
            }

            if (action == "clickQuery") {
                this.currentPage = 1;
            }

            let queryParams = Object.assign({}, this.queryForm);
            queryParams["suiteId"] = this.suiteID;
            // queryParams["productId"] = this.productID
            queryParams["suiteType"] = this.suiteType;
            queryParams["pageSize"] = this.pageSize;
            queryParams["pageNo"] = this.currentPage;
            queryParams["testplanId"] = this.testplanId;
            queryParams["sprintId"] = this.sprintID;
            queryParams.priority = queryParams.priority.join(",");

            // 
            this.$api.sprintCaseTable
                .getSuiteCases(queryParams)
                .then((res) => {
                    if (res.success) {
                        this.total = res.data.total;
                        this.tableData = res.data.rows;

                        // 
                        if (this.currentModule == "bindPlanAndCaseDialog") {
                            this.checkCaseRow(
                                res.data.testplanRelatedCaseGuids
                            );
                            this.guidsOfRelatedOldCases =
                                res.data.testplanRelatedCaseGuids;
                        }
                        if (resolve) {
                            resolve(res.success);
                        }
                    } else {
                        this.$message.error(res.msg);
                        if (reject) {
                            reject(res.success);
                        }
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                    if (reject) {
                        reject(res.success);
                    }
                });
        },
        // 
        mergeColumns({ row, column, rowIndex, columnIndex }) {
            if (row.children.length) {
                if (columnIndex == 1) {
                    return [1, 7];
                } else if (columnIndex > 1) {
                    return [0, 0];
                }
            }
        },
        // 
        getRowClassName(object) {
            if (object.row.children.length) {
                return "row-class-for-colspan";
            } else {
                return "row-class";
            }
        },
        // 
        expandAll() {
            this.$nextTick(function () {
                const els = this.$el.getElementsByClassName(
                    "el-table__expand-icon"
                );
                for (let i = 0; i < els.length; i++) {
                    if (
                        els[i]
                            .getAttribute("class")
                            .indexOf("el-table__expand-icon--expanded") == -1
                    ) {
                        // 
                        els[i].click();
                    }
                }
            });
        },

        // 
        checkCaseRow(testplanRelatedCaseGuids) {
            this.$nextTick(() => {
                for (let j = 0; j < this.tableData.length; j++) {
                    //id
                    for (
                        let i = 0;
                        i < this.tableData[j].children.length;
                        i++
                    ) {
                        if (
                            testplanRelatedCaseGuids &&
                            testplanRelatedCaseGuids.indexOf(
                                this.tableData[j].children[i].guid
                            ) != -1
                        ) {
                            this.$refs.myTable.toggleRowSelection(
                                this.tableData[j].children[i],
                                true
                            );
                        }
                    }
                }
            });
        },

        // 
        // title 
        // editingStatus 
        openCaseDialog(title, editingStatus) {
            if (!this.caseDialogLoaded) {
                // 
                this.caseDialogLoaded = true;
            }

            this.caseDialogTitle = title;
            this.caseDialogEditing = editingStatus;

            if (!this.showCaseDialog) {
                // 
                this.showCaseDialog = true;
            }
        },
        // 
        newCase() {
            let executionPhase = [];
            for (let item in this.executedEachSprintDict) {
                if (this.executedEachSprintDict[item].default) {
                    executionPhase = [item];
                    break;
                }
            }
            this.caseData = {
                suiteId: this.suiteID,
                productId: this.productID,
                sprintId: this.sprintID,
                suiteType: this.suiteType,
                customNo: "",
                name: "",
                createrName: "",
                updaterName: "",
                priority: "P3",
                executedEachSprint: "",
                executionMethod: "handwork",
                executionPhase: executionPhase,
                suitePath: this.suitePath,
                precondition: "",
                steps: [],
                postcondition: "",
                tags: [],
                desc: "",
            };

            this.operation = "newCase";
            this.openCaseDialog("", true); // 
        },
        // 
        clickRow(row, column, event) {
            let handleFunc = () => {
                let counter = 0; // 
                let caseRowFound = false; // 
                for (let i = 0; i < this.tableData.length; i++) {
                    let testSuite = this.tableData[i];

                    if (testSuite.children.length) {
                        for (let i = 0; i < testSuite.children.length; i++) {
                            counter += 1;
                            let testCase = testSuite.children[i];
                            if (testCase.id == row.id) {
                                caseRowFound = true;
                                break;
                            }
                        }
                        if (caseRowFound) {
                            break;
                        }
                    }
                }
                this.caseOrder =
                    counter + (this.currentPage - 1) * this.pageSize;

                this.caseData = {
                    suiteId: row.suiteId,
                    productId: row.productId,
                    sprintId: row.sprintId,
                    suiteType: row.suiteType,
                    caseId: row.id,
                    guid: row.guid,
                    customNo: row.customNo,
                    name: row.name,
                    createrName: row.createrName,
                    updaterName: row.updaterName, // 
                    priority: row.priority,
                    executedEachSprint: row.executedEachSprint,
                    executionMethod: row.executionMethod,
                    executionPhase: row.executionPhase,
                    suitePath: row.suitePath,
                    precondition: row.precondition,
                    steps: JSON.parse(row.steps),
                    postcondition: row.postcondition,
                    desc: row.desc,
                    tags: row.tags,
                };
                this.operation = "viewCase";
                this.row = row;
                this.openCaseDialog("", false);
            };

            // 
            if (!row.children.length && column && column.label != "") {
                if (this.caseDialogEditing) {
                    this.$confirm(
                        "?",
                        "",
                        {
                            confirmButtonText: "",
                            cancelButtonText: "",
                            type: "warning",
                            cancelButtonClass: "btn-custom-cancel",
                        }
                    )
                        .then(() => {
                            handleFunc();
                        })
                        .catch(() => {});
                } else {
                    handleFunc();
                }
            }
        },
        // 
        viewCase(index, row) {
            this.caseData = {
                caseId: row.id, // ID
                guid: row.guid,
                suiteId: row.suiteId,
                productId: row.productId,
                sprintId: row.sprintId,
                suiteType: row.suiteType, // 
                customNo: row.customNo, // 
                name: row.name, // 
                createrName: row.createrName, // 
                updaterName: row.updaterName, // 
                priority: row.priority, // 
                executedEachSprint: row.executedEachSprint,
                executionMethod: row.executionMethod,
                executionPhase: row.executionPhase,
                suitePath: row.suitePath, // 
                precondition: row.precondition, // 
                steps: JSON.parse(row.steps), // 
                postcondition: row.postcondition, //
                desc: row.desc, // 
                tags: row.tags, // 
            };

            this.operation = "viewCase";
            this.row = row;
            this.openCaseDialog("", false);
        },
        // 
        copyCase(index, row) {
            this.caseData = {
                guid: row.guid,
                suiteId: row.suiteId,
                sprintId: row.sprintId,
                productId: row.productId,
                suiteType: row.suiteType, // 
                customNo: row.customNo, // 
                name: row.name + "-", // 
                createrName: "",
                updaterName: "", // 
                priority: row.priority, // 
                executedEachSprint: row.executedEachSprint,
                executionMethod: row.executionMethod,
                executionPhase: row.executionPhase,
                suitePath: row.suitePath, // 
                precondition: row.precondition, // 
                steps: JSON.parse(row.steps), // 
                postcondition: row.postcondition, //
                desc: row.desc, // 
                tags: row.tags, // 
            };

            this.row = row;
            this.operation = "copyCase";
            this.openCaseDialog("", true);
        },
        // caseGuidArray guid
        deleteCases(caseGuidArray) {
            this.elementLoadingText = "";
            this.loading = true;
            // 
            this.$api.sprintCaseTable
                .deleteTestCases({
                    suiteId: this.suiteID,
                    guids: caseGuidArray,
                    suiteType: this.suiteType,
                    delBaseCasesCascade: this.confirmRadioValue,
                })
                .then((res) => {
                    this.loading = false;
                    if (res.success) {
                        // 
                        this.total = this.total - caseGuidArray.length;

                        this.$message.success(res.msg);

                        for (let i = 0; i < caseGuidArray.length; i++) {
                            for (let x = 0; x < this.tableData.length; x++) {
                                // idID
                                let caseRowFound = false; // 
                                if (this.tableData[x].children.length) {
                                    // 
                                    for (
                                        let y = 0;
                                        y < this.tableData[x].children.length;
                                        y++
                                    ) {
                                        if (
                                            this.tableData[x].children[y]
                                                .guid == caseGuidArray[i]
                                        ) {
                                            caseRowFound = true;
                                            this.tableData[x].children.splice(
                                                y,
                                                1
                                            ); // 
                                            if (
                                                !this.tableData[x].children
                                                    .length
                                            ) {
                                                // 
                                                this.tableData.splice(x, 1);
                                            }

                                            break;
                                        }
                                    }
                                    if (caseRowFound) {
                                        //  // 
                                        break;
                                    }
                                }
                            }

                            //  // //
                            if (!this.tableData.length && this.total != 0) {
                                let totalPages = Math.ceil(
                                    this.total / this.pageSize
                                );

                                if (this.currentPage > totalPages) {
                                    // 
                                    this.currentPage -= 1;
                                } else {
                                    // 
                                }

                                this.queryRows();
                                return;
                            }

                            // tableData()
                            this.tableData = Object.assign([], this.tableData);
                        }
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.loading = false;
                    this.$message.error(res.msg || res.message);
                });
        },

        // 
        deleteOneCase(index, row) {
            if (this.showCaseDialog) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.confirmRadioValue = "1";
            const h = this.$createElement;

            var comfirmMessage = undefined;
            if (this.suiteType == "sprint") {
                comfirmMessage = h("div", null, [
                    h("div", ""),
                    h("br"),
                    h("span", null, [
                        h(
                            "el-radio",
                            {
                                attrs: {
                                    label: "1",
                                    value: this.confirmRadioValue,
                                },
                                ref: "radio1",
                                // key: this._uid,
                                nativeOn: {
                                    input: (event) => {
                                        this.confirmRadioValue = "1";
                                        this.$nextTick(() => {
                                            this.$refs.radio1.value = "1";
                                            this.$refs.radio2.value = "1";
                                        });
                                    },
                                },
                            },
                            ""
                        ),
                        h("span", "    "),
                    ]),
                    h("span", [
                        h(
                            "el-radio",
                            {
                                attrs: {
                                    label: "2",
                                    value: this.confirmRadioValue,
                                },
                                ref: "radio2",
                                // key: this._uid + 1,

                                nativeOn: {
                                    input: (event) => {
                                        this.confirmRadioValue = "2";
                                        this.$nextTick(() => {
                                            this.$refs.radio1.value = "2";
                                            this.$refs.radio2.value = "2";
                                        });
                                    },
                                },
                            },
                            ""
                        ),
                    ]),
                ]);
            } else {
                comfirmMessage = "";
            }

            this.$msgbox({
                title: "",
                message: comfirmMessage,
                showCancelButton: true,
                confirmButtonText: "",
                cancelButtonText: "",
                cancelButtonClass: "btn-custom-cancel",
            })
                // this.$confirm("?", "", {
                //     confirmButtonText: "",
                //     cancelButtonText: "",
                //     type: "warning",
                //     cancelButtonClass: "btn-custom-cancel"
                // })
                .then(() => {
                    // 
                    this.$api.sprintCaseTable
                        .deleteTestCase({
                            suiteId: row.suiteId,
                            caseId: row.id,
                            guid: row.guid,
                            suiteType: row.suiteType,
                            delBaseCasesCascade: this.confirmRadioValue,
                        })
                        .then((res) => {
                            if (res.success) {
                                // 
                                this.total = this.total - 1;
                                this.$message.success(res.msg);
                                for (
                                    let x = 0;
                                    x < this.tableData.length;
                                    x++
                                ) {
                                    let caseRowFound = false; // 
                                    if (this.tableData[x].children.length) {
                                        // 
                                        for (
                                            let y = 0;
                                            y <
                                            this.tableData[x].children.length;
                                            y++
                                        ) {
                                            if (
                                                this.tableData[x].children[y]
                                                    .guid == row.guid
                                            ) {
                                                caseRowFound = true;
                                                this.tableData[
                                                    x
                                                ].children.splice(y, 1); // 

                                                if (
                                                    !this.tableData[x].children
                                                        .length
                                                ) {
                                                    // 
                                                    this.tableData.splice(x, 1);
                                                }

                                                break;
                                            }
                                        }
                                        if (caseRowFound) {
                                            // 
                                            break;
                                        }
                                    }
                                }

                                if (!this.tableData.length && this.total != 0) {
                                    // 

                                    let totalPages = Math.ceil(
                                        this.total / this.pageSize
                                    );

                                    if (this.currentPage > totalPages) {
                                        // 
                                        this.currentPage -= 1;
                                    } else {
                                        // 
                                    }
                                    this.queryRows();
                                    return;
                                }

                                // tableData(())
                                this.tableData = Object.assign(
                                    [],
                                    this.tableData
                                );
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },

        //
        onSelectAllRow(selection) {
            // selection
            // tableData
            if (selection.indexOf(this.tableData[0]) > -1) {
                // 
                // 
                for (let i = 0; i < this.tableData.length; i++) {
                    for (
                        let j = 0;
                        j < this.tableData[i].children.length;
                        j++
                    ) {
                        this.$refs.myTable.toggleRowSelection(
                            this.tableData[i].children[j],
                            true
                        );
                    }
                }
            } else {
                // 
                this.$refs.myTable.clearSelection();
            }
        },
        //,
        onSelectRow(selection, row) {
            let selected;
            if (!selection.length) {
                selected = false; // 
            } else {
                for (let i = 0; i < selection.length; i++) {
                    if (selection[i].guid == row.guid) {
                        // 
                        selected = true;
                        break;
                    } else {
                        // 
                        selected = false;
                    }
                }
            }

            // \\
            if (row.children.length) {
                for (let i = 0; i < row.children.length; i++) {
                    this.$refs.myTable.toggleRowSelection(
                        row.children[i],
                        selected
                    );
                }
            } else {
                // 
                // 
                for (let i = 0; i < this.tableData.length; i++) {
                    // 
                    let suiteRowFound = false;
                    for (
                        let j = 0;
                        j < this.tableData[i].children.length;
                        j++
                    ) {
                        if (this.tableData[i].children[j].guid == row.guid) {
                            suiteRowFound = true;
                            if (selected) {
                                // 
                                let suiteChecked = false;
                                for (let x = 0; x < selection.length; x++) {
                                    if (
                                        selection[x].guid ==
                                        this.tableData[i].guid
                                    ) {
                                        suiteChecked = true;
                                        break;
                                    }
                                }
                                if (!suiteChecked) {
                                    // 
                                    this.$refs.myTable.toggleRowSelection(
                                        this.tableData[i],
                                        true
                                    );
                                }
                            }
                            break;
                        }
                    }
                    if (suiteRowFound) {
                        break;
                    }
                }
            }
        },
        // 
        batchDeleteCases() {
            if (this.showCaseDialog) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.confirmRadioValue = "1";
            const h = this.$createElement;
            var comfirmMessage = undefined;
            if (this.suiteType == "sprint") {
                comfirmMessage = h("div", null, [
                    h("div", ""),
                    h("br"),
                    h("span", null, [
                        h(
                            "el-radio",
                            {
                                attrs: {
                                    label: "1",
                                    value: this.confirmRadioValue,
                                },
                                ref: "radio1",
                                key: this._uid,
                                nativeOn: {
                                    input: (event) => {
                                        this.confirmRadioValue = "1";
                                        this.$nextTick(() => {
                                            this.$refs.radio1.value = "1";
                                            this.$refs.radio2.value = "1";
                                        });
                                    },
                                },
                            },
                            ""
                        ),
                        h("span", "    "),
                    ]),
                    h("span", [
                        h(
                            "el-radio",
                            {
                                attrs: {
                                    label: "2",
                                    value: this.confirmRadioValue,
                                },
                                ref: "radio2",
                                key: this._uid + 1,

                                nativeOn: {
                                    input: (event) => {
                                        this.confirmRadioValue = "2";
                                        this.$nextTick(() => {
                                            this.$refs.radio1.value = "2";
                                            this.$refs.radio2.value = "2";
                                        });
                                    },
                                },
                            },
                            ""
                        ),
                    ]),
                ]);
            } else {
                comfirmMessage = "";
            }

            this.$msgbox({
                title: "",
                message: comfirmMessage,
                showCancelButton: true,
                confirmButtonText: "",
                cancelButtonText: "",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    let caseGuidArray = []; // guid
                    let rowsSelected = this.$refs.myTable.selection;

                    for (let i = 0; i < rowsSelected.length; i++) {
                        if (!rowsSelected[i].children.length) {
                            // 
                            caseGuidArray.push(rowsSelected[i].guid);
                        }
                    }

                    if (!caseGuidArray.length) {
                        this.$alert("", "", {
                            confirmButtonText: "",
                        });
                        this.loading = false;
                        return;
                    }

                    this.deleteCases(caseGuidArray);
                })
                .catch(() => {});
        },
        // 
        importTestCases() {
            if (!this.suiteID) {
                this.$message.error(
                    ""
                );
            }
            this.elementLoadingText = "";
            this.dataForImportCase["suiteType"] = this.suiteType;
            this.dataForImportCase["suiteId"] = this.suiteID;
            this.dataForImportCase["productId"] = this.productID;
            this.importCaseDialogVisible = true;
        },

        // 
        copyTestCasesToSprint() {
            this.casesCopyToSprint = [];
            let rowsSelected = this.$refs.myTable.selection;
            for (let i = 0; i < rowsSelected.length; i++) {
                if (!rowsSelected[i].children.length) {
                    // 
                    this.casesCopyToSprint.push(rowsSelected[i]);
                }
            }

            if (!this.casesCopyToSprint.length) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }
            this.copyCaseToSprintDlgVisible = true;
        },
        // 
        copyCases() {
            let caseArray = []; // guid
            let rowsSelected = this.$refs.myTable.selection;

            for (let i = 0; i < rowsSelected.length; i++) {
                if (!rowsSelected[i].children.length) {
                    // 
                    caseArray.push(rowsSelected[i]);
                }
            }

            if (!caseArray.length) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.$store.commit("setTestcasesCut", []); // 
            this.$store.commit("setTestcasesCopied", caseArray);
            this.$message.success("");
        },
        // 
        cutCases() {
            let caseArray = []; // guid
            let rowsSelected = this.$refs.myTable.selection;
            for (let i = 0; i < rowsSelected.length; i++) {
                if (!rowsSelected[i].children.length) {
                    // 
                    caseArray.push(rowsSelected[i]);
                }
            }

            if (!caseArray.length) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.$store.commit("setTestcasesCopied", []); // 
            this.$store.commit("setTestcasesCut", caseArray);
            this.$message.success("");
        },

        // (Excel)
        exportTestCases() {
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.elementLoadingText =
                        "";
                    this.loading = true;
                    let queryParams = Object.assign({}, this.queryForm);
                    queryParams["suiteId"] = this.suiteID;
                    // queryParams["productId"] = this.productID
                    queryParams["suiteType"] = this.suiteType;
                    queryParams["testplanId"] = this.testplanId;
                    queryParams.priority = queryParams.priority.join(",");

                    // 
                    this.$api.sprintCaseTable
                        .exportTestCases(queryParams)
                        .then((res) => {
                            let link = document.createElement("a");
                            let blob = new Blob([res.data], {
                                type: "application/vnd.ms-excel",
                            });
                            link.style.display = "none";
                            link.href = window.URL.createObjectURL(blob);
                            // link.setAttribute(
                            //     "download",
                            //     "" +
                            //         this.$customUtils.dateTimeHandler.datetimeToString(
                            //             new Date()
                            //         ) +
                            //         ".xlsx"
                            // );
                            let disposition =
                                res.headers["content-disposition"];
                            let filename = decodeURI(
                                disposition.replace("attachment;filename=", "")
                            );

                            link.setAttribute("download", filename);

                            document.body.appendChild(link);
                            link.click();
                            document.body.removeChild(link);
                            this.loading = false;
                        })
                        .catch((res) => {
                            this.loading = false;
                            if (
                                Object.prototype.toString.call(
                                    res.response.data
                                ) == "[object Blob]"
                            ) {
                                let reader = new FileReader();
                                reader.onload = (e) => {
                                    let responseData = JSON.parse(
                                        e.target.result
                                    );
                                    this.$message.error(
                                        res.msg ||
                                            res.message + ":" + responseData.msg
                                    );
                                };
                                reader.readAsText(res.response.data);
                            } else {
                                this.$message.error(res.msg || res.message);
                            }
                        });
                })
                .catch(() => {
                    this.loading = false;
                });
        },
        // (XMind)
        exportXmindTestCases() {
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    this.loading = true;
                    let queryParams = Object.assign({}, this.queryForm);
                    queryParams["suiteId"] = this.suiteID;
                    // queryParams["productId"] = this.productID
                    queryParams["suiteType"] = this.suiteType;
                    queryParams["testplanId"] = this.testplanId;
                    queryParams.priority = queryParams.priority.join(",");

                    // 
                    this.$api.sprintCaseTable
                        .exportXmindTestCases(queryParams)
                        .then((res) => {
                            let link = document.createElement("a");
                            let blob = new Blob([res.data], {
                                type: "application/octet-stream",
                            });
                            link.style.display = "none";
                            link.href = window.URL.createObjectURL(blob);
                            let disposition =
                                res.headers["content-disposition"];
                            let filename = decodeURI(
                                disposition.replace("attachment;filename=", "")
                            );

                            link.setAttribute("download", filename);

                            document.body.appendChild(link);
                            link.click();
                            document.body.removeChild(link);
                            this.loading = false;
                        })
                        .catch((res) => {
                            this.loading = false;
                            if (
                                Object.prototype.toString.call(
                                    res.response.data
                                ) == "[object Blob]"
                            ) {
                                let reader = new FileReader();
                                reader.onload = (e) => {
                                    let responseData = JSON.parse(
                                        e.target.result
                                    );
                                    this.$message.error(
                                        res.msg ||
                                            res.message + ":" + responseData.msg
                                    );
                                };
                                reader.readAsText(res.response.data);
                            } else {
                                this.$message.error(res.msg || res.message);
                            }
                        });
                })
                .catch(() => {
                    this.loading = false;
                });
        },
        // 
        batchUpdateTestCases() {
            if (this.showCaseDialog) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }
            this.elementLoadingText = "";
            this.casesInfoForUpdate = []; // guid
            let rowsSelected = this.$refs.myTable.selection;
            for (let i = 0; i < rowsSelected.length; i++) {
                if (!rowsSelected[i].children.length) {
                    // 
                    this.casesInfoForUpdate.push({
                        guid: rowsSelected[i].guid,
                        suiteId: rowsSelected[i].suiteId,
                        suite_type: rowsSelected[i].suiteType,
                        detailSuiteType: rowsSelected[i].detailSuiteType,
                        testplanId: rowsSelected[i].testplanId,
                    });
                }
            }

            if (!this.casesInfoForUpdate.length) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (!this.updateCasesDlgVisible) {
                // 
                this.updateCasesDlgVisible = true;
            }
        },

        // 
        clearExecutionMethod() {
            this.queryForm.executionMethod = "";
        },
        // 
        sortChange(object) {
            let order = object.order;
            let property = object.prop;
            if (!property) {
                // 
                property = "-id";
            } else {
                property = this.$customUtils.stringHandler.humpToUnderline(
                    property
                );
                if (property == "create_time") {
                    property = "id";
                }
            }

            if (order == "descending") {
                if (property == "priority") {
                    this.queryForm.sort = "-" + property + ",-id";
                } else {
                    this.queryForm.sort = "-" + property;
                }
            } else {
                if (property == "priority") {
                    this.queryForm.sort = property + ",id";
                } else {
                    this.queryForm.sort = property;
                }
            }
            this.queryRows();
        },
        // 
        getTestPhases() {
            this.$api.testPhase
                .getTestPhasesDetails({
                    fields: "name,code,default",
                })
                .then((res) => {
                    if (res.success) {
                        for (let i = 0; i < res.data.length; i++) {
                            let label = res.data[i].name;
                            let value = res.data[i].code;
                            let defualt = res.data[i].default;
                            let temp = {
                                label: label,
                                value: value,
                                defualt: defualt,
                            };
                            this.executedEachSprintDict[value] = {};
                            this.executedEachSprintDict[value][
                                "executedEachSprint"
                            ] = "N";
                            this.executedEachSprintDict[value][
                                "executionPhase"
                            ] = label;
                            this.executedEachSprintDict[value][
                                "default"
                            ] = defualt;
                            this.executionPhaseOptions.push(temp);
                        }
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        toggleQueryItems() {
            this.isQueryItemsCollapsed = !this.isQueryItemsCollapsed;
        },
        getInputWidthStyle(string, initWidth) {
            let style = "width:" + initWidth + "px";
            if (string) {
                let tempWidth = this.$customUtils.stringHandler.getTextPixelWith(
                    string,
                    "400 13.3333px Arial"
                ); //
                if (tempWidth > initWidth - 32) {
                    //110px - 32px  //  32padding-left + padding-right + border-left + border-right
                    style = "width:" + (tempWidth + 40) + "px"; // 40=32+8 # 8 
                }
            }
            return style;
        },
    },
    watch: {
        suiteID: function () {
            this.currentPage = 1;
            this.queryRows();
            this.caseDialogEditing = false;
            this.showCaseDialog = false;
            this.caseDialogLoaded = false;
        },
    },
    computed: {
        ...mapState({
            suiteType: (state) => state.sprintCaseSuite.suiteType,
            suitePath: (state) => state.sprintCaseSuite.suitePath,
            suiteID: (state) => state.sprintCaseSuite.suiteID,
            parentID: (state) => state.sprintCaseSuite.parentID,
        }),
        iscurrSuiteRoot: {
            get() {
                if (this.parentID == -1) {
                    return true;
                } else {
                    return false;
                }
            },
        },
        createTime: {
            get() {
                if (this.queryForm.startTime && this.queryForm.endTime) {
                    return [this.queryForm.startTime, this.queryForm.endTime];
                } else {
                    return [];
                }
            },
            set(value) {
                if (value) {
                    this.queryForm.startTime = value[0];
                    this.queryForm.endTime = value[1];
                } else {
                    this.queryForm.startTime = "";
                    this.queryForm.endTime = "";
                }
            },
        },
    },
    created() {
        try {
            this.getTestPhases();
        } catch (err) {
            this.$message.error(err.message);
        }
    },
    mounted() {
        // this.queryRows(); // // watch
    },

    updated() {
        this.setTableBodySize(); // 
        this.expandAll(); // 
        if (this.currentModule == "bindPlanAndCaseDialog") {
            this.$nextTick(() => {
                this.checkCaseRow(this.guidsOfRelatedOldCases);
            });
        }
    },
};
</script>

<style lang="scss">
.sprint-testcase-table {
    .el-table__header-wrapper {
        th {
            padding-top: 3px !important;
            padding-bottom: 3px !important;
        }
    }

    // 
    .el-table__placeholder {
        width: 0px;
    }

    .el-table__indent {
        padding-left: 0px !important;
    }

    .operation-col-cell-class .cell {
        padding-left: 5px !important;
        padding-right: 5px !important;
    }

    .operation-col-cell-class .cell .el-button--danger {
        padding-left: 10px !important;
        padding-right: 10px !important;
    }

    .operation-col-cell-class .cell .el-button {
        margin-left: 5px !important;
        margin-right: 1px;
    }

    .row-class-for-colspan {
        background: rgb(231, 229, 229);
        td {
            padding-top: 3px !important;
            padding-bottom: 3px !important;
        }

        .cell {
            color: grey;
            height: auto !important;
            width: 100% !important;
        }
    }

    .row-class {
        td {
            padding-top: 3px !important;
            padding-bottom: 3px !important;
        }
        .cell {
            padding-left: 2px;
            padding-right: 5px;
            height: auto !important;
        }
    }
}
</style>



